import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faHome as homeIcon} from '@fortawesome/free-solid-svg-icons/faHome';
import React, {useMemo} from 'react';
import {connect} from 'react-redux';
import Link from '../../core/Link/Link';
import {userRoleSelector} from '../../../store/selectors';
import {USER_ROLES} from '../../../constants';

function Breadcrumbs({rootRoute = 'projectsPage', items, userRole}) {
  const logoRoute = useMemo(() => (userRole === USER_ROLES.SUPER_ADMIN.key ? 'superAdminHomePage' : userRole === USER_ROLES.ACCOUNTS_EXECUTIVE.key ? 'accountsExecutivePage' : rootRoute), [userRole]);

  return (
    <div>
      <span id="BreadcrumbHome"><Link route={logoRoute}><FontAwesomeIcon icon={homeIcon} /></Link></span>
      {
        items.map((item, i) => (
          <span id={`Breadcrumb-${i}`} key={`${i}${item.to}`}>
&nbsp;/&nbsp;
            <Link route={item.route} params={item.params} to={item.to}>{item.content}</Link>
          </span>
        ))
      }
    </div>
  );
}

const mapStateToProps = (state) => ({
  userRole: userRoleSelector(state)
});

export default connect(mapStateToProps)(Breadcrumbs);
