import React from 'react';
import {connect} from 'react-redux';
import Modal from 'react-bootstrap/Modal';
import ProgressBar from 'react-bootstrap/ProgressBar'
import { loadingBackdropWithProgressSelector } from '../../../store/selectors';

function LoadingBackdropWithProgress({show, percentage}) {
  return (
    <Modal show={show} backdrop="static" animation={false} centered>
      <div className="progressBarWrapper">
          <ProgressBar animated striped now={percentage} label={`${percentage}%`}/>
      </div>
    </Modal>
  );
}

const mapStateToProps = (state) => {
    const lbState = loadingBackdropWithProgressSelector(state);
    return {
      show: lbState.show,
      percentage: lbState.loadingPercent
    };
  };

export default connect(mapStateToProps)(LoadingBackdropWithProgress);
