import React, {useState} from 'react';
import {Formik} from 'formik';
import * as Yup from 'yup';
import {withServices} from 'reaf';
import {connect} from 'react-redux';
import {pick} from 'lodash';
import messages from '../../../../locales';
import TemplateModelFormComponent from './TemplateModelFormComponent';
import {PROPERTY_TYPES, PROPERTY_TYPES as PROPERTY_TYPESDefault} from '../../../constants';
import {companyTemplatesList} from '../../../store/selectors';


const ModelFormSchema = Yup.object().shape({
  modelName: Yup.string().required(messages.errors.templateName),
  assetType: Yup.mixed()
    .oneOf(Reflect.ownKeys(PROPERTY_TYPES), 'Please select a valid asset type.')
});

function TemplateModelForm({
  initialValues, onSubmitSuccess, bindSubmission, templateModelService, companyUUID, templateUUID,
  companyTemplates
}) {
  const [templateFile, setTemplateFile] = useState(null);
  const [reportTemplateFile, setReportTemplateFile] = useState(null);
  let {assetTypes} = companyTemplates[templateUUID];
  assetTypes = assetTypes ? pick(PROPERTY_TYPESDefault, assetTypes) : PROPERTY_TYPESDefault;

  async function onSubmit(values, actions) {
    actions.setStatus({});
    try {
      if (!templateFile) {
        alert('Please select model file');
        actions.setSubmitting(false);
        return;
      }
      await templateModelService.uploadTemplateModel(companyUUID, templateUUID, {...values, modelFile: templateFile, reportTemplateFile});
      onSubmitSuccess();
    } catch (error) {
      actions.setStatus({error: error.message});
    }
    actions.setSubmitting(false);
  }

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={ModelFormSchema}
      onSubmit={onSubmit}
      component={formikProps => (
        <TemplateModelFormComponent
          {...formikProps}
          bindSubmission={bindSubmission}
          selectedFile={templateFile}
          onFileChange={e => setTemplateFile(e.target.files[0])}
          reportTemplateFile={reportTemplateFile}
          setReportTemplateFile={setReportTemplateFile}
          onRemoveFile={() => setTemplateFile(null)}
          assetTypes={assetTypes} />
      )
      }
    />
  );
}

const mapStateToProps = state => ({
  companyTemplates: companyTemplatesList(state)
});

export default connect(mapStateToProps)(withServices('templateModelService')(TemplateModelForm));
