import React, {
  useEffect, useRef, useState, useMemo
} from 'react';
import '@grapecity/spread-sheets/styles/gc.spread.sheets.css';
import '@grapecity/spread-sheets/styles/gc.spread.sheets.excel2013white.css';
import {Container, Row, Col} from 'react-bootstrap';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import {IO} from '@grapecity/spread-excelio';
import {faCalendar as calendarIcon} from '@fortawesome/free-solid-svg-icons/faCalendar';
import ColorPicker from '../ColorPicker/ColorPicker';
import {
  cellNumberFormat, sheetIndex, PROPERTY_TYPES, DocumentFileTypes, affixComponentName
} from '../../../constants';
import styles from './SpreadSheet.module.scss';
import WorkbookManager from '../../../services/WorkbookManager';
import SheetIcon from '../Button/SheetIcon';
import IconButton from '../Button/IconButton';
import DocExtractionTaggingInfo from '../../app/DocExtractionTaggingInfo/DocExtractionTaggingInfo';

function SpreadSheet({
  className = '', activeSheetName = null, data, onDataLoad, readonly, showToolbar, isProjectReport = false, onInitManager, sheetCount, assetType = '', docType = '', taggingInfo = {}, onSaveData, onDownloadXLS, hasAutoWidth = false, sheetHeight, onToggleSubHeader, onRefereceWindowOpen, onSlidePaneOpen, onToggleMonths
}) {
  const spreadEl = useRef(null);
  const formulaEl = useRef(null);
  const statusEl = useRef(null);
  const cellRangeEl = useRef(null);
  const [manager, setManager] = useState(null);
  const [formatCopied, setFormatCopied] = useState(false);
  const [showSubHeader, setShowSubHeader] = useState(true);

  const isRentRollWithMultiFamily = useMemo(() => (assetType === PROPERTY_TYPES.MULTIFAMILY.key || assetType === PROPERTY_TYPES.HEALTHCARE.key) &&
    docType === DocumentFileTypes.RENT_ROLL.key, [assetType, docType]);

  const isDocumentOperatingStatement = useMemo(() => docType !== DocumentFileTypes.RENT_ROLL.key, [docType]);

  useEffect(() => {
    const _manager = new WorkbookManager();
    setManager(_manager);
    return () => {
      _manager.destroy();
      setManager(null);
    };
  }, []);

  useEffect(() => {
    if (manager) {
      manager.init(spreadEl, formulaEl, statusEl, cellRangeEl, sheetCount, {
        formatCopied: () => setFormatCopied(true),
        copiedFormatCleared: () => setFormatCopied(false),
        docType,
        assetType
      });
      if (onInitManager) {
        onInitManager(manager);
      }
    }
  }, [manager, onInitManager, sheetCount]);

  useEffect(() => {
    if (manager) {
      manager.setData(data, hasAutoWidth);
      if (onDataLoad) {
        onDataLoad({sheetNames: manager.getSheetNames()});
      }
    }
  }, [data, hasAutoWidth, manager, onDataLoad]);

  useEffect(() => {
    if (manager && activeSheetName) {
      manager.setActiveSheet(activeSheetName);
    }
  }, [manager, activeSheetName])

  useEffect(() => {
    manager && manager.setReadonly(readonly);
  }, [manager, readonly]);

  function onSave() {
    if (onSaveData) {
      const data = manager.getWorkbookData();
      onSaveData(data);
    }
  }

  function onUndo() {
    manager.undo();
  }

  function onRedo() {
    manager.redo();
  }

  function onFreeze() {
    manager.freeze();
  }

  function onUnfreeze() {
    manager.unfreeze();
  }

  function onFormatChange(format) {
    manager.setFormat(format);
  }

  function setFilter() {
    manager.setFilter();
  }

  function onSortAscending() {
    manager.sort(undefined, true);
  }

  function onSortDescending() {
    manager.sort(undefined, false);
  }

  function setForeColor(color) {
    manager.setColor(undefined, true, color.hex);
  }

  function setBackColor(color) {
    manager.setColor(undefined, false, color.hex);
  }

  function downloadXLS() {
    if (onDownloadXLS) {
      const data = manager.getWorkbookDataWithoutSheetProtection();
      const io = new IO();
      io.save(data, (blob) => {
        onDownloadXLS(blob);
      }, console.log);
    }
  }

  function copyFormat() {
    if (!formatCopied) {
      manager.copyFormat();
    } else {
      manager.clearCopyFormat();
    }
  }

  function hAlign(alignment) {
    manager.setHAlign(alignment);
  }

  function cellTextFormat(format) {
    manager.setTextFormat(format);
  }

  function onSlideAffixClik(selectedTab, componentName) {
    onSlidePaneOpen(selectedTab, componentName);
  }

  function onReverseRowSignClick() {
    manager.setReverseSignToRow();
  }

  function onToggleSubHeaderIconClick() {
    setShowSubHeader(!showSubHeader);
    onToggleSubHeader();
  }


  return (
    <div className={`${className} vertical-section`} style={sheetHeight}>
      {
        showToolbar &&
        (
          <React.Fragment>
            <Container fluid className="p-0">
              <Row>
                <Col>
                  <div className={`${styles.spreadsheetsContainer}`}>
                    <div className={`${styles.spreadsheetToolbarWrapper}`}>
                      <div className={`${styles.spreadsheetToolbar}`}>
                        <SheetIcon className={`${styles.undoIcon}`} onClick={onUndo} title="Undo" />
                        <SheetIcon className={`${styles.redoIcon}`} onClick={onRedo} title="Redo" />
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <SheetIcon className={`${styles.lockIcon}`} onClick={onFreeze} title="Freeze Rows/Columns" />
                        <SheetIcon className={`${styles.unlockIcon}`} onClick={onUnfreeze} title="Unfreeze Rows/Columns" />
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <SheetIcon className={`${styles.ascendingIcon}`} onClick={onSortAscending} title="Sort Ascending" />
                        <SheetIcon className={`${styles.descendingIcon}`} onClick={onSortDescending} title="Sort Descending" />
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <div className={`position-relative d-inline-block ${styles.docsToolbarButton}`}>
                          <DropdownButton title="123" className={`dropdown-btn-xs ${styles.spreadsheetToolbarDropdownButton}`} variant="light" size="sm">
                            <Dropdown.Item onClick={() => onFormatChange('')}>General</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange(0)}>Integer</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange(cellNumberFormat)}>Number</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('_([$$-en-US]* #,##0.00_);_([$$-en-US]* (#,##0.00);_([$$-en-US]* "-"??_);_(@_)')}>Currency</Dropdown.Item>
                            <Dropdown.Item
                              onClick={() => onFormatChange('_($* #,##0.00_);_($* (#,##0.00);_($* \'-\'??_);_(@_)')}
                            >
                              Accounting
                              {' '}
                            </Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('mm/dd/yyyy;@')}>Short Date</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('dddd, mmmm dd, yyyy')}>Long Date</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('h:mm:ss AM/PM')}>Time</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('0%')}>Percentages</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('# ?/?')}>Fraction</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('0.00E+00')}>Scientific</Dropdown.Item>
                            <Dropdown.Item onClick={() => onFormatChange('@')}>Text</Dropdown.Item>
                          </DropdownButton>
                        </div>
                        <ColorPicker onSetColor={color => setBackColor(color)}><SheetIcon className={`${styles.fillCellColorIcon}`} title="Fill Cell Color" /></ColorPicker>
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <SheetIcon className={`${styles.boldIcon}`} onClick={() => cellTextFormat('bold')} title="Bold" />
                        <SheetIcon className={`${styles.italicIcon}`} onClick={() => cellTextFormat('italic')} title="Italic" />
                        <SheetIcon className={`${styles.underlineIcon}`} onClick={() => cellTextFormat('underline')} title="Underline" />
                        <ColorPicker onSetColor={color => setForeColor(color)}><SheetIcon className={`${styles.textColorIcon}`} title="Text Color" /></ColorPicker>
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <SheetIcon className={formatCopied ? `${styles.paintIcon} text-warning` : `${styles.paintIcon} text-auto`} onClick={copyFormat} title="Paint Format" />
                        <SheetIcon className={`${styles.autofilterIcon}`} onClick={setFilter} title="Apply Row Filter" />
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        <SheetIcon className={`${styles.alignLeftIcon}`} onClick={() => hAlign('left')} title="Align Left" />
                        <SheetIcon className={`${styles.alignCenterIcon}`} onClick={() => hAlign('center')} title="Align Center" />
                        <SheetIcon className={`${styles.alignRightIcon}`} onClick={() => hAlign('right')} title="Align Right" />
                        <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                        {
                          (isDocumentOperatingStatement && !isProjectReport) && (
                            <>
                              <SheetIcon className={`${styles.reverseSignIcon}`} id="reverseRowSign" onClick={onReverseRowSignClick} title="Reverse Row Sign" />
                              <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                              <SheetIcon className={`${styles.calendarMonthsIcon}`} id="toggleMonths" onClick={() => onToggleMonths()} title="Show/Hide Months" />
                            </>
                          )
                        }

                      </div>
                    </div>
                    <div className={`${styles.docsToolbar} ${styles.spreadsheetSideToolbar}`}>


                      {/* {
                        isRentRollWithMultiFamily && (
                          <SheetIcon id="configCC" className={`${styles.configChageCodeIcon}`} size="lg" onClick={() => onSlideAffixClik('chargeCodeConfig')} title="Rent Roll Configuration" />
                        )
                      } */}
                      {/* {
                        isDocumentOperatingStatement && (
                          <SheetIcon id="osSummary" className={`${styles.configChageCodeIcon}`} size="lg" onClick={() => onSlideAffixClik(affixComponentName.OPERATING_STATEMENT)} title="Document Summary Sheet" />
                        )
                      } */}
                      {
                        onSaveData && (
                          <DocExtractionTaggingInfo docType={docType} taggingInfo={taggingInfo} />
                        )
                      }

                      {
                        !onSaveData && (
                          <span className="text-danger small flex-grow-1 text-right mr-4">* Any changes you make will not be saved. Please download the report to keep a copy.</span>
                        )
                      }
                      <div className={`position-relative d-inline-block ${styles.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                      <SheetIcon className={`${styles.downloadFileIcon}`} id="DownloadXLSXFile" size="lg" onClick={downloadXLS} title="Download Workbook" />
                      {
                        !isProjectReport && (
                          <>
                          <SheetIcon className={`${styles.saveFileIcon}`} size="lg" id="SaveXLSXFile" onClick={onSave} hidden={!onSaveData} title="Save Workbook" />
                          <SheetIcon className={`${styles.referenceWindowIcon}`} id="refWindow" size="lg" onClick={onRefereceWindowOpen} title="Open Reference Window" />
                          <SheetIcon className={`${styles.keyboardShortcutsIcon}`} id="keyboardShortcuts" size="lg" onClick={() => onSlideAffixClik(affixComponentName.SHORTCUT_KEYS, affixComponentName.SHORTCUT_KEYS)} title="Keyboard Shortcuts" />  
                          </>  
                        )
                      }
                    </div>
                  </div>
                </Col>
              </Row>
              <Row className={`pr-2 ${styles.formulaBarInputContainer}`}>
                <Col xs="auto" className="pr-0">
                  <span id="SSCellBox" className={`position-relative d-inline-block ${styles.formulaBarLabel}`} ref={cellRangeEl}>A1</span>
                </Col>
                <Col className={`${styles.formulaBarInput}`}>
                  <input id="SSFormulaBox" ref={formulaEl} className={`position-relative d-inline-block m-0 ${styles.formulaBar} ${styles.cellInput}`} style={{flexGrow: 0}} />
                </Col>
              </Row>
            </Container>
          </React.Fragment>
        )
      }
      <div className="vertical-section position-relative">
        <div ref={spreadEl} className="fill-parent" />
      </div>
      <div ref={statusEl} style={{flexGrow: 0}} />
    </div>
  );
}


export default SpreadSheet;
