import React from 'react';
import Col from 'react-bootstrap/Col';
import ProjectTile from './ProjectTile';
import style from './ProjectsGridView.module.scss';

export default function ProjectsGridView({
  projects, onNeedSupport, selectedProject, onClearTagging, onDelete, onEdit, onOpen, onSelect, documentsSummary
}) {
  return (
    projects.map((project, index) => (
      <Col key={project.uuid} className={`flex-sm-grow-0 ${style.col}`}>
        <ProjectTile
          id={`Project-${index}`}
          project={project}
          selected={project === selectedProject}
          onClearTagging={() => onClearTagging(project)}
          onDelete={() => onDelete(project)}
          onEdit={() => onEdit(project)}
          onOpen={() => onOpen(project)}
          onClick={e => onSelect(project)}
          onNeedSupport={() => onNeedSupport(project)}
          documentsSummary={documentsSummary[project.uuid]}
        />
      </Col>
    ))
  );
}
