import React from 'react';

export default function useBool(initial = false) {
  const [b, setB] = React.useState(initial);
  const set = React.useCallback(() => setB(true), []);
  const reset = React.useCallback(() => setB(false), []);
  const toggle = React.useCallback(() => setB(!b), [b]);
  return [b, set, reset, toggle, setB];
}
