import React from 'react';
import {connect} from 'react-redux';
import {withServices} from 'reaf';
import {Container, Row, Col} from 'react-bootstrap';
import {
  currentCompanySelector,
  companiesDetailsSelector
} from '../../../store/selectors';
import SuperAdminDashboard from '../SuperAdminDashboard/SuperAdminDashboard';
import CompanyTemplateList from '../../app/CompanyTemplateList/CompanyTemplateList';
import Breadcrumbs from '../../app/Breadcrumbs/Breadcrumbs';

function TemplateListPageHeader({companyUUID, companiesDetails}) {
  return (
    <Container fluid className="p-2 light border-bottom-light-blue sa-breadcrumb">
      <Row className="align-items-center">
        <Col>
          <Breadcrumbs
            rootRoute="superAdminHomePage"
            items={[{route: 'superAdminPage', content: companiesDetails[companyUUID].name},
              {to: '#', content: 'Templates'}]} />
        </Col>
      </Row>
    </Container>
  );
}

function TemplateListPage({companyUUID, companiesDetails}) {
  return (
    <SuperAdminDashboard>
      <TemplateListPageHeader companyUUID={companyUUID} companiesDetails={companiesDetails} />
      <CompanyTemplateList />
    </SuperAdminDashboard>
  );
}
const mapStateToProps = state => ({
  companyUUID: currentCompanySelector(state),
  companiesDetails: companiesDetailsSelector(state)
});
export default connect(mapStateToProps)(withServices('templateService')(TemplateListPage));
