import {createDuck} from 'reaf';

const initialState = {
  show: false,
  type: '',
  message: '',
  position: 'bottom'
};

const [
  {showToastr, hideToastr},
  reducer
] = createDuck('autouw/notificationToastr', {
  _initialState: initialState,

  showToastr: (type, message, position) => state => ({
    ...state,
    show: true,
    type,
    message,
    position
  }),

  hideToastr: () => state => ({
    ...state,
    ...initialState
  })
});

export {
  showToastr,
  hideToastr
};

export default reducer;
