import React, {useState, useEffect, useCallback} from 'react';
import {connect} from 'react-redux';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import {withServices} from 'reaf';
import {faListUl as listIcon} from '@fortawesome/free-solid-svg-icons/faListUl';
import {faThLarge as gridIcon} from '@fortawesome/free-solid-svg-icons/faThLarge';
import {faPlus} from '@fortawesome/free-solid-svg-icons/faPlus';
import {faSearch as searchIcon} from '@fortawesome/free-solid-svg-icons/faSearch';
import {
  allProjectsAsListSelector,
  documentsSummarySelector,
  uiStateSelector
} from '../../../store/selectors';
import {updateProjectView} from '../../../store/uiState';
import ProjectsView from '../../app/ProjectsView/ProjectsView';
import ProjectFormModal from '../../app/ProjectFormModal/ProjectFormModal';
import LoggedInUserPage from '../LoggedInUserPage/LoggedInUserPage';
import {PROPERTY_TYPES} from '../../../constants';
import Breadcrumbs from '../../app/Breadcrumbs/Breadcrumbs';
import ProjectsPageTour from './ProjectsPageTour';
import AddProjectsTour from './AddProjectsTour';
import TourIconButton from '../../app/IconButtons/TourIconButton';
import {markFirstTourDone} from '../../../store/productTour';
import style from './ProjectsPage.module.scss';
import IconButton from '../../core/Button/IconButton';

function ProjectsPageHeader({
  propertyTypeFilter, onSearch, onFilterChange, onHelpClick, isListView, store
}) {
  const toggleListGridView = () => {
    store.dispatch(updateProjectView(!isListView));
  };

  return (
    <Container fluid className="bg-light border-bottom sub-header">
      <Row className="align-items-center">
        <Col sm="auto" className="align-middle p2">
          <Breadcrumbs items={[]} />
        </Col>
        <Col />
        <Col sm="auto" className="p-0 mr-2">
          <span className="search-icon">
            <IconButton icon={searchIcon} variant className="border-none" />
          </span>
          <input
            id="ProjectNameFilter"
            type="text"
            className={`form-control form-control-sm form-control-xs d-inline-block search-bar-inputField ${style.formControlXs}`}
            placeholder="Search property name"
            onChange={e => onSearch(e.target.value)}
            style={{width: 200}}
          />
        </Col>
        {
          !isListView &&
          (
            <Col xs="auto" className="p-0 mr-2">
              <Form.Control id="AssetTypeFilter" as="select" size="sm" value={propertyTypeFilter} defaultValue={propertyTypeFilter} onChange={e => onFilterChange(e.target.value)} className={`form-control-xs ${style.formControlXs}`}>
                <option value="">All Asset Types</option>
                {
                  Reflect.ownKeys(PROPERTY_TYPES).map(key => (
                    <option value={key} key={key}>{PROPERTY_TYPES[key].label}</option>
                  ))
                }
              </Form.Control>
            </Col>
          )
        }
        <Col xs="auto" className="p-0">
          <IconButton id="ToggleDocumentListGridView" title={isListView ? "Grid View" : "List View"} onClick={() => toggleListGridView()} className="btn-xs mr-2 smallIconButton" icon={isListView ? gridIcon : listIcon} />
        </Col>
        <Col sm="auto" className="pl-0 tourIconBtn">
          <TourIconButton onClick={onHelpClick} className={`btn-xs ${style.buttonAlignment}`} id="ShowTourButton" />
        </Col>
      </Row>
    </Container>
  );
}

function ProjectsPageSubHeader({onAddProjectClick}) {
  return (
    <Container fluid className="bg-white subHeader-ms">
      <Row className="align-items-center">
        <Col sm="auto">
          <h5 className={`mb-0 ${style.headingAligment}`}>Property</h5>
        </Col>
        <Col sm="auto pl-0">
          <IconButton title="Add Property" className="btn-circle btn-shadow" variant="outline-secondary" size="sm" icon={faPlus} onClick={onAddProjectClick} id="AddProjectButton" />
        </Col>
        <Col />
      </Row>
    </Container>
  );
}

function ProjectsPage({
  projects, documentsSummary, startTour, store, isProjectListView
}) {
  const [showAddProjectModal, setShowAddProjectModal] = useState(false);
  const [filteredProjects, setFilteredProjects] = useState(projects);
  const [searchText, setSearchText] = useState('');
  const [propertyTypeFilter, setPropertyTypeFilter] = useState('');
  const [showTour, setShowTour] = useState(startTour);
  const [showAddProjectTour, setShowAddProjectTour] = useState(false);
  const [tourStep, setTourStep] = useState(0);

  useEffect(() => {
    let filtered = projects;
    if (searchText) {
      filtered = filtered.filter(
        project => project.name.toLowerCase().indexOf(searchText.toLowerCase()) > -1,
      );
    }
    if (propertyTypeFilter) {
      filtered = filtered.filter(
        project => project.assetType === propertyTypeFilter,
      );
    }
    setFilteredProjects(filtered);
  }, [projects, searchText, propertyTypeFilter]);

  const addProjectClicked = useCallback(() => {
    setShowAddProjectModal(true);
    setTourStep(2);
  }, []);

  const onAddProjectTutorial = useCallback(() => {
    store.dispatch(markFirstTourDone('projectsPage'));
    setShowTour(false);
    setShowAddProjectTour(true);
  }, []);

  const closeTours = useCallback(() => {
    setShowTour(false);
    setShowAddProjectTour(false);
    store.dispatch(markFirstTourDone('projectsPage'));
  }, []);

  const closeAddProjectModal = useCallback(() => {
    setShowAddProjectModal(false);
    closeTours();
  }, []);

  const onHelpClick = useCallback(() => {
    setShowTour(true);
    document.getElementById('projectsContainer').scrollTop = 0;
  }, [showTour]);

  return (
    <LoggedInUserPage>
      <ProjectsPageHeader
        isListView={isProjectListView}
        onSearch={setSearchText}
        onFilterChange={setPropertyTypeFilter}
        propertyTypeFilter={propertyTypeFilter}
        onHelpClick={onHelpClick}
        onAddProjectHelp={() => setShowAddProjectTour(true)}
        store={store}
      />
      <ProjectsPageSubHeader onAddProjectClick={addProjectClicked} />
      <ProjectsView setPropertyTypeFilter={setPropertyTypeFilter} propertyTypeFilter={propertyTypeFilter} isListView={isProjectListView} projects={filteredProjects} documentsSummary={documentsSummary} />
      <ProjectFormModal show={showAddProjectModal} onHide={closeAddProjectModal} />
      {showTour && <ProjectsPageTour show={showTour} onClose={closeTours} onAddProjectTutorial={onAddProjectTutorial} isListView={isProjectListView} />}
      {showAddProjectTour && <AddProjectsTour show={showAddProjectTour} onClose={closeTours} step={tourStep} />}
    </LoggedInUserPage>
  );
}

const mapStateToProps = (state) => ({
  projects: allProjectsAsListSelector(state),
  documentsSummary: documentsSummarySelector(state),
  isProjectListView: uiStateSelector(state).isProjectListView,
  startTour: false
});

export default connect(mapStateToProps)(withServices('store')(ProjectsPage));
