import React, {useMemo} from 'react';
import {connect} from "react-redux";
import IconButton from '../../core/Button/IconButton';
import {currentDocumentTypeSelector, currentProjectSelector} from "../../../store/selectors";
import {DocumentFileTypes, PROPERTY_TYPES} from "../../../constants";
import './DocExtractionAffixWrapper.scss';
import {
  isOSBaseType, isRetailBaseType, isMFBaseType, isHotelBaseType
} from '../../../utils/utils';

function MfRRSidebar({onAffixOpen}) {
  return (
    <nav className="nav nav-tabs">
      <IconButton
        className="nav-link affixVerticalNav chargeCode" title="Charge Code Configuration" onClick={() => {
          onAffixOpen('chargeCode');
        }} variant="link" id="configCC" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav unitStatus" title="Unit Status Configuration" onClick={() => {
          onAffixOpen('unitStatus');
        }} variant="link" id="unitStatusTabButton" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav floorPlan" title="Floor Plan Configuration" onClick={() => {
          onAffixOpen('floorPlan');
        }} variant="link" id="floorPlanTabButton" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav analysis" title="Lease Trend Analysis" onClick={() => {
          onAffixOpen('analysis');
        }} variant="link" id="analysisTabButton" showIcon={false} />
    </nav>
  );
}

function MfOSSidebar({onAffixOpen}) {
  return (
    <nav className="nav nav-tabs">
      <IconButton
        className="nav-link affixVerticalNav documentSummary" title="Summary and Analysis" onClick={() => {
          onAffixOpen('documentSummary');
        }} variant="link" id="osAffixButton" showIcon={false} />
    </nav>
  );
}

function RetailRRSidebar({onAffixOpen}) {
  return (
    <nav className="nav nav-tabs">
      <IconButton
        className="nav-link affixVerticalNav chargeCode" title="Charge Code Configuration" onClick={() => {
          onAffixOpen('chargeCode');
        }} variant="link" id="configCC" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav occupancy" title="Unit Status Configuration" onClick={() => {
          onAffixOpen('occupancy');
        }} variant="link" id="occupancyTabButton" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav leaseType" title="Lease Type Configuration" onClick={() => {
          onAffixOpen('leaseType');
        }} variant="link" id="leaseTypeTabButton" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav tenantType" title="Lease Tenant Type Configuration" onClick={() => {
          onAffixOpen('tenantType');
        }} variant="link" id="tenantTypeTabButton" showIcon={false} />
      <IconButton
        className="nav-link affixVerticalNav analysis" title="Lease Expiration Schedule" onClick={() => {
          onAffixOpen('analysis');
        }} variant="link" id="analysisTabButton" showIcon={false} />
    </nav>
  );
}

function DocExtractionAffixSidebar({onAffixOpen, currentDocumentType, project}) {
  const assetType = useMemo(() => {
    if (isRetailBaseType(project.assetType)) {
      return PROPERTY_TYPES.RETAIL.key;
    } else if (
      isMFBaseType(project.assetType) || isHotelBaseType(project.assetType)
    ) {
      return PROPERTY_TYPES.MULTIFAMILY.key;
    }
  }, [project]);

  const documentType = useMemo(() => {
    if (isOSBaseType(currentDocumentType)) {
      return DocumentFileTypes.OPERATING_STATEMENT.baseType;
    } else if (currentDocumentType === DocumentFileTypes.RENT_ROLL.key) {
      return DocumentFileTypes.RENT_ROLL.key;
    }
  }, [currentDocumentType]);


  return (
    <div className="affixVerticalTabs">
      {isOSBaseType(documentType) &&
      <MfOSSidebar onAffixOpen={onAffixOpen} />
      }

      {assetType === PROPERTY_TYPES.MULTIFAMILY.key && documentType === DocumentFileTypes.RENT_ROLL.key &&
      <MfRRSidebar onAffixOpen={onAffixOpen} />
      }

      {assetType === PROPERTY_TYPES.RETAIL.key && documentType === DocumentFileTypes.RENT_ROLL.key &&
      <RetailRRSidebar onAffixOpen={onAffixOpen} />
      }
    </div>


  );
}

const mapStateToProps = state => ({
  project: currentProjectSelector(state),
  currentDocumentType: currentDocumentTypeSelector(state)
});

export default connect(mapStateToProps)(DocExtractionAffixSidebar);
