import React from 'react';
import {
  Navbar, Button, Form
} from 'react-bootstrap';
import {debounce} from 'lodash';

const TicketListHeader = ({toggleListView, filterTickets}) => {
  const searchHandler = debounce(filterTickets, 300);

  return (
    <Navbar bg="light" expand="lg">
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Button variant="outline-primary" type="button" className="mr-sm-2" hidden onClick={toggleListView}>Toggle View</Button>
        <Form inline onSubmit={(e) => e.preventDefault()}>
          <Form.Control
            className="mr-sm-2"
            id="inlineFormInputName2"
            onChange={(e) => searchHandler(e.target.value)}
            placeholder="Search"
          />
        </Form>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default TicketListHeader;
