import {dilite} from 'reaf';
import AuthService from '../services/AuthService';
import Routes from '../routes/Routes';
import ApiClient from '../services/ApiClient';
import ProjectsService from '../services/ProjectsService';
import App from '../../App';
import DocumentsService from '../services/DocumentsService';
import CompanyService from '../services/CompanyService';
import CompanyListService from '../services/CompanyListService';
import config from './index';
import UserService from '../services/UserService';
import TemplateService from '../services/TemplateService';
import TemplateModelService from '../services/TemplateModelService';
import TicketService from '../services/TicketService';
import ToastrService from '../services/ToastrService';
import UtilsService from '../services/UtilsService';
import AccountsExecutiveService from '../services/AccountsExecutiveService';
import ProspectsService from '../services/ProspectsService';
import ProjectExporterService from '../services/ProjectExporterService';
import SsoConfigurationService from '../services/SsoConfigurationService';


const {ctor, value} = dilite;

const services = {
  authService: ctor(
    AuthService,
    ['store'],
    [['apiClient', 'setApiClient'], ['router', 'setRouter']],
  ),


  routes: ctor(
    Routes,
    [
      'authService', 'projectsService', 'documentsService',
      'companyService', 'companyListService', 'store',
      'appBaseUrl', 'userService', 'templateService',
      'templateModelService', 'accountsExecutiveService',
      'prospectsService', 'projectExporterService',
      'ticketService'
    ],
  ),

  apiClient: ctor(
    ApiClient,
    [],
    [['authService', 'setAuthService']],
  ),

  projectsService: ctor(
    ProjectsService,
    ['apiClient', 'store'],
  ),

  documentsService: ctor(
    DocumentsService,
    ['apiClient', 'store', 'projectsService'],
  ),

  companyService: ctor(
    CompanyService,
    ['apiClient', 'store', 'companyListService', 'authService'],
    [['toastrService', 'setToastrService']]
  ),

  companyListService: ctor(
    CompanyListService,
    ['apiClient', 'store'],
  ),

  userService: ctor(
    UserService,
    ['apiClient', 'store'],
  ),

  templateService: ctor(
    TemplateService,
    ['apiClient', 'store'],
  ),

  utilsService: ctor(
    UtilsService,
    ['apiClient', 'store'],
  ),

  templateModelService: ctor(
    TemplateModelService,
    ['apiClient', 'authService', 'store'],
  ),

  toastrService: ctor(
    ToastrService,
    ['store']
  ),

  accountsExecutiveService: ctor(
    AccountsExecutiveService,
    ['apiClient', 'store']
  ),

  prospectsService: ctor(
    ProspectsService,
    ['apiClient', 'store']
  ),

  projectExporterService: ctor(
    ProjectExporterService,
    ['apiClient', 'store']
  ),

  ticketService: ctor(
    TicketService,
    ['apiClient', 'store']
  ),
  ssoConfigurationService: ctor(
    SsoConfigurationService,
    ['apiClient', 'store']
  ),

  rootComponent: value(App),
  appBaseUrl: value(config.appBaseUrl)

};

export default services;
