import React, {useMemo, useCallback} from 'react';
import {Field} from 'formik';
import {isNil} from 'lodash';
import FieldErrorMessage from '../FieldErrorMessage/FieldErrorMessage';
import {
  PROPERTY_TYPES, DocumentFileTypes, EXCLUDE_DOC_FILE_TYPES, HIDE_RENT_ROLL_ASSET_TYPES
} from '../../../constants';
import Checkbox from '../../core/Checkbox/Checkbox';
import DatePicker from '../../core/DatePicker/DatePicker';

function CompanyFieldsComponent({values, setFieldValue}) {
  const documentTypes = useMemo(() => Reflect.ownKeys(DocumentFileTypes).filter(docType => !EXCLUDE_DOC_FILE_TYPES.includes(docType)));
  const {company: {paymentDueDate, restrictRentRoll, isSSOEnabled}} = values;

  const getAssetTypeDisabled = useCallback((key) => {
    const docTypes = values.company.documentTypes;
    return docTypes.length === 1 && docTypes[0] === 'RENT_ROLL' && HIDE_RENT_ROLL_ASSET_TYPES.includes(key);
  }, [values.company.documentTypes]);

  const onDocTypeChange = (docTypes) => {
    if (docTypes.length === 1 && docTypes[0] === 'RENT_ROLL') {
      setFieldValue('company.assetTypes', [...values.company.assetTypes.filter((assetType) => !HIDE_RENT_ROLL_ASSET_TYPES.includes(assetType))]);
    }
  };

  return (
    <>
      <div className="form-group">
        <label htmlFor="company.name">Company Name</label>
        <Field id="company.name" className="form-control" name="company.name" type="text" />
        <FieldErrorMessage name="company.name" />
      </div>

      <div className="form-group">
        <label htmlFor="company.assetTypes" style={{display: "block", fontWeight: 500}}>Asset Types</label>
        <div style={{marginLeft: '-5px'}}>
          {
            Reflect.ownKeys(PROPERTY_TYPES).map(key => (
              <Checkbox name="company.assetTypes" disabled={getAssetTypeDisabled(key)} className="ml-2 mr-1" value={key} key={key} label={PROPERTY_TYPES[key].label} />
            ))
          }
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="company.documentTypes" style={{display: "block", fontWeight: 500}}>Allowed document types</label>
        <div style={{marginLeft: '-5px'}}>
          {
            documentTypes.map(key => (
              <Checkbox name="company.documentTypes" onChange={onDocTypeChange} className="ml-2 mr-1" value={key} key={key} label={DocumentFileTypes[key].label} />
            ))
          }
        </div>
      </div>

      <div className="form-group">
        <label htmlFor="company.restrictRentRoll" style={{display: "block", fontWeight: 500}}>
          Restrict Rent Roll:
          <Field type="checkbox" name="company.restrictRentRoll" checked={restrictRentRoll} />
        </label>
      </div>

      <div className="form-group">
        <label htmlFor="company.notificationText">Notification Text</label>
        <Field id="company.notificationText" className="form-control" name="company.notificationText" type="text" />
        <FieldErrorMessage name="company.notificationText" />
      </div>

      <div className="form-group">
        <label htmlFor="company.phone">Phone Number</label>
        <Field id="company.phone" className="form-control" name="company.phone" type="text" />
        <FieldErrorMessage name="company.phone" />
      </div>

      <div className="form-group">
        <label htmlFor="company.numberOfLicenses">Number of Licenses</label>
        <Field id="company.numberOfLicenses" className="form-control" name="company.numberOfLicenses" type="number" />
        <FieldErrorMessage name="company.numberOfLicenses" />
      </div>

      <div className="form-group">
        <label htmlFor="company.address.address">Address</label>
        <Field id="company.address.address" className="form-control" name="company.address.address" type="text" />
        <FieldErrorMessage name="company.address.address" />
        {!!values.company.address && !!values.company.address.address && values.company.address.address.length > 0 && (
          <>
            <label htmlFor="company.address.zip">Postal Code</label>
            <Field id="company.address.zip" className="form-control" name="company.address.zip" type="text" />
            <FieldErrorMessage name="company.address.zip" />
            <label htmlFor="company.address.country">Country</label>
            <Field id="company.address.country" className="form-control" name="company.address.country" type="text" />
            <FieldErrorMessage name="company.address.country" />
          </>
        )}
      </div>

      { isNil(paymentDueDate) && (
        <div className="form-group">
          <label htmlFor="paymentDueDate">Payment Due Date</label>
          <Field
            name="paymentDueDate"
            id="paymentDueDate"
            component={DatePicker}
            selected={values.paymentDueDate || null}
            className="form-control"
            dateFormat="MM/dd/yyyy"
          />
        </div>
      )}
      {
        (!isNil(paymentDueDate)) && (paymentDueDate !== '') && (
          <div className="form-group">
            <label htmlFor="paymentDueDate">Payment Due Date</label>
            <Field className="form-control" value={values.previousDueDate} type="text" readOnly />
            <label htmlFor="paymentStatus">Payment Status</label>
            <Field id="paymentStatus" className="form-control" name="paymentStatus" component="select">
              <option value="Pending">Pending</option>
              <option value="Completed">Completed</option>
            </Field>
          </div>
        )}
    </>
  );
}

export default CompanyFieldsComponent;
