import React, {useCallback} from 'react';
import {useDropzone} from 'react-dropzone';
import {withServices} from 'reaf';
import textDocumentIcon from '../../../assets/images/text-documents.svg';
import style from './DocumentFileDrop.module.scss';
import './DocumentFileDrop.scss';

function DocumentFileDrop({project, documentsService}) {
  const onDrop = useCallback((acceptedFiles) => {
    documentsService.createDocuments(project, acceptedFiles).catch(console.log);
  }, [project, documentsService]);
  const {getRootProps, getInputProps} = useDropzone({onDrop});

  return (
    <div className="uploadDocumentFileDrop">
      <div {...getRootProps({className: 'vertical-section justify-content-center bg-light dropzone-file-uploads-container'})}>
        <input {...getInputProps()} />
        <div className={`text-center ${style.documentFileDragdrop}`}>
          <div className={`${style.documentFileIcon}`}>
           <img src={textDocumentIcon} alt="text document icon" />
          </div>
          <h1>Drop your Rent Roll and Operating Statement files here</h1>
          <p>
Or use the&nbsp;
            <strong className="text-primary cursor-pointer">Browse Files</strong>
            {' '}
&nbsp;button
            {' '}
          </p>
          <small>
Please upload&nbsp;
            <span className="text-primary">.pdf, .xlsx</span>
            {' '}
&nbsp;file formals.
          </small>
        </div>
      </div>
    </div>
  );
}

export default withServices('documentsService')(DocumentFileDrop);
