import React, {
  useMemo, useCallback, useState
} from 'react';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import {faBroom as clearTaggingIcon} from '@fortawesome/free-solid-svg-icons/faBroom';
import {faTrash as deleteProjectIcon} from '@fortawesome/free-solid-svg-icons/faTrash';
import {faPencilAlt as editProjectIcon} from '@fortawesome/free-solid-svg-icons/faPencilAlt';
import 'font-awesome/css/font-awesome.min.css';
import {connect} from 'react-redux';
import Autocomplete from 'react-autocomplete';
import {Form} from 'react-bootstrap';
import {pick} from 'lodash';
import {PROPERTY_TYPES} from '../../../constants';
import folderFile from '../../../assets/images/folder.svg';
import style from './ProjectsListView.module.scss';
import ItemWithContextMenu from '../ItemTile/ItemWithContextMenu';
import ProjectContextMenu from '../ProjectContextMenu/ProjectContextMenu';
import {loggedInCompanySelector, userAllowedTemplatesSelector} from '../../../store/selectors';
import IconButton from '../../core/Button/IconButton';
import './ProjectsListView.scss';

const sizePerPage = 100;
const autocompleteMenuStyle = {
  borderRadius: '3px',
  boxShadow: '0 2px 12px rgba(0, 0, 0, 0.1)',
  background: 'rgba(255, 255, 255, 0.9)',
  padding: '2px 0',
  fontSize: '90%',
  position: 'fixed',
  overflow: 'auto',
  maxHeight: '50%',
  margin: 0
};

const formatDate = str => {
  if (!str) {
    return;
  }
  const date = new Date(str);
  return `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
};

const assetIconFormatter = (_, project) => (PROPERTY_TYPES[project.assetType] ? PROPERTY_TYPES[project.assetType].label : '');

function ProjectsListView({
  onNeedSupport, propertyTypeFilter, setPropertyTypeFilter,
  projects, templates, selectedProject, onClearTagging, allowedAssetTypes,
  onDelete, onEdit, onOpen, onSelect, documentsSummary = {}
}) {
  const [autoCompleteValue, setAutoCompleteValue] = useState('');
  const trClassFormat = useCallback((project) => (selectedProject && selectedProject.uuid === project.uuid ? `${style.selectedProject} productItem` : 'productItem'), [selectedProject]);

  const assetTypesOptions = useMemo(() => Reflect.ownKeys(pick(PROPERTY_TYPES, allowedAssetTypes)).reduce((assetTypeOption, propertyType) => {
    assetTypeOption[propertyType] = PROPERTY_TYPES[propertyType].label;
    return assetTypeOption;
  }, {}), [allowedAssetTypes]);

  const selectRow = useMemo(() => ({
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: (project, isSelected) => { isSelected ? onSelect(project) : onSelect(null); },
    selected: selectedProject ? [selectedProject.uuid] : []
  }), [selectedProject]);

  const modelUsedOptions = useMemo(() => Reflect.ownKeys(templates).reduce((modelUsed, templateUuid) => {
    const currentTemplate = templates[templateUuid];
    if (propertyTypeFilter && currentTemplate.assetTypes) {
      if (currentTemplate.assetTypes.includes(propertyTypeFilter)) {
        modelUsed[templateUuid] = currentTemplate.templateName;
      }
    } else {
      modelUsed[templateUuid] = currentTemplate.templateName;
    }
    return modelUsed;
  }, {}), [templates, propertyTypeFilter]);

  const options = useMemo(() => ({
    onRowDoubleClick: onOpen,
    sizePerPage,
    sizePerPageList: [{
      text: '100', value: 100
    }, {
      text: '200', value: 200
    },
    {
      text: '500', value: 500
    }],
    prePage: 'Prev',
    nextPage: 'Next',
    sortIndicator: true
  }), [onOpen]);

  const templateNameFormatter = useCallback((templateUuid) => templates[templateUuid] && templates[templateUuid].templateName, [templates]);

  const projectContextMenu = useCallback((project, TileComponent, openOnClick = false) => (
    <ItemWithContextMenu
      TileComponent={TileComponent}
      openOnClick={openOnClick}
      ContextMenuComponent={({hideContextMenu}) => (
        <ProjectContextMenu
          project={project}
          hideContextMenu={hideContextMenu}
          onClearTagging={() => onClearTagging(project)}
          onDelete={() => onDelete(project)}
          onEdit={() => onEdit(project)}
          onOpen={() => onOpen(project)}
          onNeedSupport={() => onNeedSupport(project)}
          documentsSummary={documentsSummary[project.uuid] || {}}
        />
      )} />
  ),
  [documentsSummary]);

  const projectNameFormatter = useCallback((_, project) => projectContextMenu(project, () => project.name, false), [documentsSummary]);

  const disableClearTagging = useCallback((project) => templates[project.templateUuid].keysToMask.length > 0, [templates]);

  const actionFormatter = useCallback((_, project) => (
    <>
      <IconButton title="Edit" className="smallIconButton editProjectIcon" icon={editProjectIcon} onClick={() => onEdit(project)} />
      <IconButton title="Clear Tagging" className="smallIconButton clearTaggingIcon" icon={clearTaggingIcon} onClick={() => onClearTagging(project)} disabled={disableClearTagging(project)} />
      <IconButton title="Delete" className="smallIconButton deleteIcon" icon={deleteProjectIcon} onClick={() => onDelete(project)} />
    </>
  ), [documentsSummary]);
  const folderIconFormat = useCallback((_, project) => projectContextMenu(project, () => (<img src={folderFile} width={20} alt="file icon" />)), [documentsSummary]);


  const customModelFilter = useCallback((filterHandler) => (
    <Autocomplete
      value={autoCompleteValue}
      inputProps={{className: 'filter text-filter form-control'}}
      getItemValue={x => x.label}
      renderInput={(props) => (<input placeholder="Select Model Used" {...props} />)}
      menuStyle={autocompleteMenuStyle}
      shouldItemRender={(item, value) => item.label.toLowerCase().indexOf(value.toLowerCase()) !== -1}
      items={
        Reflect.ownKeys(modelUsedOptions).map(modelUuid => ({uuid: modelUuid, label: modelUsedOptions[modelUuid]}))
      }
      renderItem={(item, isHighlighted) => (
        <div className="autocompleteItem" key={item.uuid} style={{background: isHighlighted ? 'lightgray' : 'white'}}>
          {item.label}
        </div>
      )}

      onSelect={(_, searchItem) => {
        setAutoCompleteValue(searchItem.label);
        filterHandler({callback: searchValue => (searchItem.uuid ? searchItem.uuid === searchValue : true)});
      }}
      onChange={(e) => {
        if (!e.target.value) {
          filterHandler({callback: _ => true});
        }
        setAutoCompleteValue(e.target.value);
      }}
    />
  ), [modelUsedOptions, propertyTypeFilter, autoCompleteValue]);

  const customAssetTypeFilter = useCallback(filterHandler => (
    <Form.Control
      as="select"
      value={propertyTypeFilter}
      id="AssetTypeFilter"
      onChange={e => {
        const assetType = e.target.value;
        setPropertyTypeFilter(assetType);
        filterHandler({
          callback: searchValue => {
            if (!assetType) {
              return true;
            }
            return assetType === searchValue;
          }
        });
      }}
    >
      <option value="">All Asset Types</option>
      {Reflect.ownKeys(assetTypesOptions).map(uuid => (<option value={uuid} key={uuid}>{assetTypesOptions[uuid]}</option>))}
    </Form.Control>
  ), [assetTypesOptions, setPropertyTypeFilter, propertyTypeFilter]);

  return (
    <BootstrapTable
      containerClass="bootstrapTableCustom projectsListViewWrapper"
      pagination={projects.length > sizePerPage}
      headerContainerClass={style.projectListHeader}
      trClassName={trClassFormat}
      selectRow={selectRow}
      hover
      bordered={false}
      data={projects}
      version="4"
      options={options}
    >
      <TableHeaderColumn isKey hidden dataField="uuid"> Document uuid</TableHeaderColumn>
      <TableHeaderColumn dataField="assetType" width="60px" columnClassName={`${style.folderIcon}`} dataAlign="center" dataFormat={folderIconFormat}>  </TableHeaderColumn>
      <TableHeaderColumn dataField="name" className="headerFilter inputPlaceholder" dataFormat={projectNameFormatter} dataSort filter={{type: 'TextFilter', placeholder: 'Enter Property Name'}}>Property Name</TableHeaderColumn>
      <TableHeaderColumn
        dataField="assetType" className="headerFilter assetTypes" dataFormat={assetIconFormatter} filter={{
          type: 'CustomFilter', options: assetTypesOptions, getElement: customAssetTypeFilter
        }}>
Asset Class
        {' '}
      </TableHeaderColumn>
      <TableHeaderColumn
        dataField="templateUuid" className="headerFilter inputPlaceholder" dataFormat={templateNameFormatter} filter={{
          type: 'CustomFilter',
          getElement: customModelFilter,
          options: modelUsedOptions
        }}>
Model Used
      </TableHeaderColumn>
      <TableHeaderColumn dataField="createdAt" width="120px" dataAlign="left" dataFormat={formatDate} dataSort>Created On</TableHeaderColumn>
      <TableHeaderColumn dataField="updatedAt" width="120px" dataAlign="left" dataFormat={formatDate} dataSort>Updated On</TableHeaderColumn>
      <TableHeaderColumn dataField="action" width="120px" dataAlign="right" dataFormat={actionFormatter} />
    </BootstrapTable>
  );
}

const mapStateToProps = (state) => ({
  templates: userAllowedTemplatesSelector(state),
  allowedAssetTypes: loggedInCompanySelector(state).assetTypes
});

export default connect(mapStateToProps)(ProjectsListView);
