import React from 'react';
import Switch from 'react-switch';

const ToggleOption = ({
  label, isActive, className, onClick
}) => (
  <span
    style={{cursor: 'pointer'}}
    className={`${className} ${isActive ? 'text-primary' : 'text-muted'}`}
    onClick={onClick}>
    {label}
  </span>
);

const SwitchButton = ({
  isActive, onChange, offLabel, onLabel
}) => (
  <>
    <ToggleOption
      label={offLabel}
      isActive={!isActive}
      onClick={() => onChange(!isActive)}
      className="mr-1" />
    <Switch
      checked={isActive}
      onChange={onChange}
      width={36}
      height={16}
      handleDiameter={12}
      offColor="#007bfe"
      onColor="#007bfe"
      checkedIcon={false}
      uncheckedIcon={false}
    />
    <ToggleOption
      label={onLabel}
      isActive={isActive}
      onClick={() => onChange(!isActive)}
      className="ml-1" />
  </>
);

export default SwitchButton;
