import {resetAuth, setAuth, validateToken} from '../store/auth';
import {setAppMetaData} from '../store/metadata';
import {authSelector} from '../store/selectors';
import messages from '../../locales';
import {
  authConfig, appConfig, apiConfig, socialLogin
} from '../config';
import {USER_ROLES} from '../constants';

const AUTH_TOKEN_KEY = `${appConfig.environment}-auth`;

class AuthService {
  constructor(store) {
    this.store = store;
  }

  setRouter(router) {
    this.router = router;
  }

  setApiClient(apiClient) {
    this.apiClient = apiClient;
  }

  authState() {
    return authSelector(this.store.getState());
  }

  isLoggedIn() {
    const authState = this.authState();
    return authState && authState.token;
  }

  hasRole(role) {
    const authState = this.authState();
    return authState && authState.role === role;
  }

  isSuperAdmin() {
    const authState = this.authState();
    return authState.role === USER_ROLES.SUPER_ADMIN.key;
  }

  isAccountsExecutive() {
    const authState = this.authState();
    return authState.role === USER_ROLES.ACCOUNTS_EXECUTIVE.key;
  }

  async logoutFromOtherDevices({email, password, captchaToken}, redirect = true) {
    try {
      await this.apiClient.post(
        '/auth/revokeExistingToken',
        {email, password, captchaToken},
      );
    } catch (error) {
      if (error.status === 401) {
        throw new Error(messages.errors.auth.invalidCredentials);
      }
      throw error;
    }
  }

  async logout() {
    try {
      await this.apiClient.post('/auth/revokeToken');
    } catch (e) {
      console.log(e);
    }

    if (appConfig.isFreebieApp) {
      await this.socialLogout();
    }

    this.store.batchDispatch([
      resetAuth()
    ]);
    this.router.setRoute('loginPage');
  }

  async socialLogout() {
    return new Promise((resolve, reject) => {
      window.gapi.load('auth2', () => {
        if (window.gapi.auth2) {
          const auth2 = window.gapi.auth2.getAuthInstance();
          if (auth2) {
            auth2.signOut().then(() => {
              resolve();
            }).catch(console.error);
          } else {
            resolve();
          }
        }
      });
    });
  }

  async login({email, password, captchaToken}, redirect = true) {
    let response;
    try {
      response = await this.apiClient.post(
        '/auth/token',
        {email, password, captchaToken},
      );
    } catch (error) {
      const {status, message} = error;
      if (status === 401) {
        throw new Error(messages.errors.auth.invalidCredentials);
      } else if (status === 403 && messages.errors.auth.multipleSessions === message) {
        throw new Error(messages.errors.auth.multipleSessionsNotAllowed);
      } else if (status === 403 && messages.errors.auth.resetPassword === message) {
        this.router.setRoute('resetPasswordPage', error.error);
        return;
      }

      throw error;
    }

    const {
      user, role, token, company, eventCounts
    } = response.response;
    this.store.dispatch(setAuth(user, token, role, company, eventCounts));

    if (redirect) {
      this.router.setRoute(this.resolveRoute());
    }

    return {
      user, role, token, company
    };
  }

  async fetchUsersAuthState(queries) {
    let response;
    try {
      response = await this.apiClient.get(
        '/auth/saml/fetchAuthDetail',
        {
          headers: {Authorization: `Bearer ${queries.token}`}
        }
      );
    } catch (error) {
      const {status, message} = error;
      if (status === 401) {
        throw new Error(messages.errors.auth.invalidCredentials);
      } else if (status === 403 && messages.errors.auth.multipleSessions === message) {
        throw new Error(messages.errors.auth.multipleSessionsNotAllowed);
      } else if (status === 403 && messages.errors.auth.resetPassword === message) {
        this.router.setRoute('resetPasswordPage', error.error);
        return;
      }

      throw error;
    }

    const {
      user, role, token, company, eventCounts
    } = response.response;
    this.store.dispatch(setAuth(user, token, role, company, eventCounts));

    this.router.setRoute('loginPage');

    return {
      user, role, token, company
    };
  }

  resolveRoute() {
    let routePage = 'projectsPage';
    if (this.isSuperAdmin()) {
      routePage = 'superAdminHomePage';
    } else if (this.isAccountsExecutive()) {
      routePage = 'accountsExecutivePage';
    }

    return routePage;
  }

  async changePassword({oldPassword, newPassword}) {
    await this.apiClient.post(
      '/auth/changePassword',
      {
        oldPassword,
        newPassword
      },
    );
  }

  async validateAuthToken() {
    return this.apiClient.post('/auth/validateToken');
  }

  async requestResetPassword({email, captchaToken}) {
    try {
      await this.apiClient.post(
        '/auth/requestPasswordReset',
        {email, captchaToken},
      );
    } catch (e) {
      if (e.status === 400) {
        throw new Error('We don\'t have this email registered with us');
      } else {
        throw e;
      }
    }
  }

  async resetPassword({email, token, password}) {
    await this.apiClient.post(
      '/auth/resetPassword',
      {email, token, password},
    );
  }

  async getAppMetaData() {
    const response = await this.apiClient.get(
      '/metadata'
    );
    return response.response;
  }

  registerSideEffects(sideEffect) {
    sideEffect.boot(async ({dispatch}) => {
      const {
        user, token, role, company, eventCounts
      } = JSON.parse(localStorage.getItem(AUTH_TOKEN_KEY) || '{}');

      if (token) {
        try {
          await this.apiClient.post('/auth/validateAuthToken', {token});
          dispatch(setAuth(user, token, role, company, eventCounts));
          dispatch(validateToken(user, company, eventCounts));
        } catch (e) {
          // Invalid auth token, don't set auth and trigger authenticated stuff
        }
      }

      if (appConfig.isFreebieApp) {
        window.gapi.load('auth2', () => {
          window.gapi.auth2.init({
            client_id: socialLogin.googleClientId,
            cookiepolicy: 'single_host_origin'
          });
        });
      }
      const appMetaData = await this.getAppMetaData();
      dispatch(setAppMetaData(appMetaData));
    });

    sideEffect.after(setAuth, ([user, token, role, company, eventCounts], {dispatch}) => {
      localStorage.setItem(AUTH_TOKEN_KEY, JSON.stringify({
        user, token, role, company, eventCounts
      }));

      dispatch(validateToken(user, company, eventCounts));
    });

    sideEffect.after(resetAuth, () => {
      localStorage.removeItem(AUTH_TOKEN_KEY);
    });

    sideEffect.after(validateToken, async (_, {dispatch}) => {
      try {
        setTimeout(async () => {
          const {token} = JSON.parse(localStorage.getItem(AUTH_TOKEN_KEY) || '{}');
          if (token) {
            const {response: {user, company, eventCounts}} = await this.validateAuthToken();
            dispatch(validateToken(user, company, eventCounts));
          }
        }, authConfig.timeout * 1000);
      } catch (e) {
        console.log(e);
        await this.logout();
      }
    });
  }

  async acceptTerms() {
    const response = await this.apiClient.post('/auth/acceptTerms');
    const user = response.response;
    const {
      token, role, company, eventCounts
    } = this.authState();
    this.store.dispatch(setAuth(user, token, role, company, eventCounts));
    this.router.goBack();
  }

  async completeTrialUsersRegistration(companyDetails) {
    const response = await this.apiClient.post('/auth/completeTrialUsersRegistration', companyDetails);
    const {user, company} = response.response;
    const {token, role, eventCounts} = this.authState();
    this.store.dispatch(setAuth(user, token, role, company, eventCounts));
    this.router.goBack();
  }

  get hasAgreedTerms() {
    return this.isSuperAdmin() || this.isAccountsExecutive() || this.authState().user.hasAgreedTerms;
  }

  async socialLogin({idToken, socialLoginType}, redirect = true) {
    let response;
    try {
      response = await this.apiClient.post('/auth/social-login', {
        idToken, socialLoginType
      });
    } catch (error) {
      console.log(error);
    }
    const {
      user, role, token, company, eventCounts
    } = response.response;
    this.store.dispatch(setAuth(user, token, role, company, eventCounts));

    if (redirect) {
      this.router.setRoute(this.resolveRoute());
    }

    return {
      user, role, token, company
    };
  }

  linkedInLogin() {
    const {linkedInClientId, linkedInRedirectUri} = socialLogin;
    const linkedInCallbackUrl = `https://www.linkedin.com/oauth/v2/authorization?response_type=code&client_id=${linkedInClientId
      }&redirect_uri=${linkedInRedirectUri}&scope=r_liteprofile%20r_emailaddress`;
    window.open(linkedInCallbackUrl, "_self");
  }

  samlLogin(companySlug) {
    window.location = `${apiConfig.baseUrl}/auth/saml/${companySlug}`;
  }
}

export default AuthService;
