import React from 'react';
import {
  compact, difference,
  divide, find,
  findIndex, groupBy,
  invertBy, isArray, isEmpty, isNil,
  last, map,
  max, mergeWith, min, multiply, pick, round, some, sortBy,
  sum, sumBy,
  uniq, uniqBy
} from 'lodash';
import * as moment from 'moment';
import * as PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withServices } from 'reaf';
import GC from '@grapecity/spread-sheets';
import {
  DataSpreadSheet, HeaderStyleMap,
  rowColors,
  SOURCE_SHEET,
  SOURCE_SHEET_NAME,
  TARGET_SHEET,
  TARGET_SHEET_NAME,
} from './DataSpreadSheet';
import { rrCellChangedEvent } from './Events/RRCellChangedEvent';
import { affixComponentName, ALLOWED_RETAIL_CC_CATEGORY, cellNumberFormat, DocumentFileTypes, excludedRentRollMFColValue, PROPERTY_TYPES, RENT_ROLL_MF_COLUMN_NAME, RETAIL_CHARGE_CODE_FORMULA, sheetIndex, tenantTypeColumnCalc } from '../../../constants';
import DocExtractionTaggingInfo from '../DocExtractionTaggingInfo/DocExtractionTaggingInfo';
import { SpreadSheetToolbarButton } from '../../core/Spreadjs/Toolbar/SpreadSheetToolbarButton';
import coreStyle from '../../core/Spreadjs/SpreadSheets.module.scss';
import { arrayContainsArray, checkArrayIncludesValue, findValidKey, getKeyByValue, getPercentage, getTotalByKey, isBracketExists } from '../../../utils/utils';
import { setChargeCodeConfig, setFloorPlan, setLeaseConfig, setRetailLeaseTypeConfig, setRetailOccupancy, setRetailTenantTypeConfig, setUnitStatus } from '../../../store/currentDocument';
import { chargeCodeConfigSelector, currentDocumentFloorPlanSelector, currentDocumentUnitStatusSelector, occupancyMappingSelector, occupancySelector, retailLeaseTypeConfigSelector, retailLeaseTypeDropDownSelector, staticChargeCodeMappingSelector, staticChargeCodeSelector, tenantNameUnitStatusSelector } from '../../../store/selectors';


const {
  sourceSheetIndex, targetSheetIndex, workingSheetIndex
} = sheetIndex;

class RRSpreadSheet extends React.Component {
  constructor(props) {
    super(props);
    this.refDataSheet = React.createRef();
    this.state = {
      data: null,
    };
  }

  get datasheet() {
    return this.refDataSheet.current;
  }

  get spreadsheet() {
    return this.datasheet.spreadsheet;
  }

  get workbook() {
    return this.spreadsheet.workbook;
  }

  componentDidMount() {
    this.addStyles();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.workbookData !== this.props.workbookData && this.props.workbookData) {
      this.workbook.fromJSON(this.props.workbookData);
    } else if (prevProps.data !== this.props.data) {
      this.loadData(this.props.data);
    }
    this.props.onInitManager(this);
  }

  loadData = () => {
    const { data, workbookData, onlySource} = this.props;
    if (!(data || workbookData) || !this.datasheet || !this.spreadsheet) {
      setTimeout(() => {
        this.loadData();
        if (onlySource) {
          this.removeAllSheetsExceptSourceSheet();
        }
      }, 100);
      return;
    }

    if (workbookData) {
      this.loadDataFromWorkbookData();
    } else {
      this.loadDataFromRawData();
    }

    this.registerEvents();
    this.rentRollEvents();
  };

  loadDataFromWorkbookData = () => {
    const { workbookData } = this.props;
    this.workbook.fromJSON(workbookData);
  };

  removeAllSheetsExceptSourceSheet() {
    this.spreadsheet.suspend();
    const sheetCount = this.spreadsheet.workbook.sheets.length;
    for (let i = 1; i < sheetCount; i++) {
      this.spreadsheet.workbook.removeSheet(1);
    }
    this.spreadsheet.resume();
  }


  addStyles() {
    this.spreadsheet.addStyle(null, HeaderStyleMap.STATIC_CHARGE_CODE, {
      backColor: 'rgb(51, 51, 51)',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin',
      },
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.CHARGE_CODE, {
      backColor: 'rgb(51, 96, 51)',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin',
      },
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_DATE, {
      backColor: '#446',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin'
      },
      borderLeft: {
        color: 'black',
        lineStyle: 'thin'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_DATE_DATA, {
      backColor: '#aae',
      foreColor: '#000',
      borderLeft: {
        color: 'black',
        lineStyle: 'medium'
      },
      borderBottom: {
        color: 'grey',
        lineStyle: 'thin'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_MONTHLY, {
      backColor: '#557',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_MONTHLY_DATA, {
      backColor: '#ccf',
      foreColor: '#000',
      borderLeft: {
        color: 'grey',
        lineStyle: 'thin'
      },
      borderBottom: {
        color: 'grey',
        lineStyle: 'thin'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_PSF, {
      backColor: '#557',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin'
      },
      borderRight: {
        color: 'black',
        lineStyle: 'thin'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.RENT_STEP_PSF_DATA, {
      backColor: '#ccf',
      foreColor: '#000',
      borderBottom: {
        color: 'grey',
        lineStyle: 'thin'
      },
      borderLeft: {
        color: 'grey',
        lineStyle: 'thin'
      },
      borderRight: {
        color: 'black',
        lineStyle: 'medium'
      }
    });

    this.spreadsheet.addStyle(null, HeaderStyleMap.SUMMARY_STATIC_HEADER, {
      backColor: '#3e3d39',
      foreColor: '#fff',
      borderBottom: {
        color: 'black',
        lineStyle: 'thin'
      }
    });
  }

  loadDataFromRawData = () => {
    const { data } = this.props;
    const sourceRows = data?.source.rows || [];
    const sourceColumns = data?.source.columns || [];
    const extractedRows = data?.target.rows || [];
    const extractedColumns = data?.target.columns || [];

    const extractedHeaderRow = extractedColumns.reduce((header, column) => {
      header[column.name] = column.headerName;
      return header;
    }, {});

    const sourceHeaderRow = sourceColumns.reduce((header, column) => {
      header[column.name] = column.headerName;
      return header;
    }, {});

    this.datasheet.spreadsheet.suspend();
    this.loadSourceData(sourceColumns, [sourceHeaderRow, ...sourceRows]);
    this.loadExtractedData(extractedColumns, [extractedHeaderRow, ...extractedRows]);
    this.datasheet.setSheetData(workingSheetIndex, [], [], 'Working Sheet', false);
    this.datasheet.highlightMappedRows(data?.source, data?.target);
    this.setOutlineToGroupedColumns();
    this.datasheet.spreadsheet.resume();
  };

  loadSourceData = (columns, rows) => {
    this.datasheet.setSheetData(SOURCE_SHEET, columns, rows, SOURCE_SHEET_NAME, true, false, false);
  };

  loadExtractedData = (columns, rows) => {
    this.datasheet.setSheetData(TARGET_SHEET, columns, rows, TARGET_SHEET_NAME, false, true, true);
  };

  onTargetIdValueChange(row, newValue, oldValue, sourceColCount = -1) {
    // un-match old value
    this.datasheet.updateRowBackColorForColumnValue(SOURCE_SHEET, 0, oldValue, undefined, 0, sourceColCount);
    // match new value
    this.datasheet.updateRowBackColorForColumnValue(
      SOURCE_SHEET, 0, newValue, rowColors.mappedRow, 0, sourceColCount,
    );
  }

  registerEvents() {
    this.spreadsheet.registerEvent(this.spreadsheet.GC.Spread.Sheets.Events.CellChanged, rrCellChangedEvent, this);
    this.spreadsheet.workbook.bind(GC.Spread.Sheets.Events.CellChanged, (_, args) => {
      const {
        sheetName, newValue, oldValue
      } = args;
      if (sheetName === TARGET_SHEET_NAME && oldValue !== newValue) { 
        this.rentRollEvents()
      }
    });
     
  }

  rentRollEvents () {
    this.getUniqueUnitStatus();
    this.getUniqueFloorPlan();
    this.retrieveChargeCodes();
    this.getRetailLeaseExpirationData();
    this.retrieveChargeCodes();
    this.getRetailOccupancyStatus();
    this.getRetailLeaseType();
    this.getRetailTenantTypeData()
  }



  onSaveWorkbook() {
    const { onSaveData } = this.props;
    const workbookData = this.spreadsheet.getWorkbookData();
    onSaveData(workbookData);
  }

  validateDocumentAssetType(baseClass) {
    return PROPERTY_TYPES[this.props.assetType].baseClass === baseClass &&
      !PROPERTY_TYPES[this.props.assetType].isRentRollRestricted;
  }

  getTargetSheetRowCount() {
    this.spreadsheet.suspend();
    let serialNumbers = [];
    const rowCount = this.spreadsheet.getSheet(targetSheetIndex).getRowCount();
    for (let row = 0; row < rowCount; row++) {
      const value = this.spreadsheet.getCellValue(targetSheetIndex, row, 0);
      if (!isNil(value)) {
        serialNumbers.push(value);
      }
    }
    this.spreadsheet.resume();
    return serialNumbers.length;
  }


  getColumnOutlineLevels(sheet, columnCount) {
    this.spreadsheet.suspend();
    const outlineLevels = sheet.columnOutlines.getMaxLevel();
    const outlines = [];
    for (let index = 0, i = 0; index <= outlineLevels; index++) {
      for (let col = 0; col < columnCount; col++) {
        // Fetch group information of Outline
        const groupInfo = sheet.columnOutlines.find(col, index);
        if (!isNil(groupInfo)) {
          outlines[i] = groupInfo;
          i++;
          col = groupInfo.end;
        }
      }
    }
    this.spreadsheet.resume();
    return outlines;
  }

  setOutlineToGroupedColumns() {
    const { assetType } = this.props
    const sheet = this.spreadsheet.getSheet(targetSheetIndex);
    const sheetData = this.datasheet.getSheetData([TARGET_SHEET_NAME]);
    this.spreadsheet.suspend();
    if (!!sheetData.length && sheetData[0].columns) {
      const rentRollCols = sheetData[0].columns.filter(col => col.type && col);
      if (rentRollCols.length > 0) {
        if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
          const chargeCodeStartColIndex = findIndex(rentRollCols, ['type', 'CHARGE_CODE']);
          const staticCCStartColIndex = findIndex(rentRollCols, ['type', 'STATIC_CHARGE_CODE']);
          if (chargeCodeStartColIndex !== -1 && staticCCStartColIndex !== -1) {
            const endColIndex = rentRollCols.length - chargeCodeStartColIndex;
            const chargeCodeEndColumnIndex = chargeCodeStartColIndex - staticCCStartColIndex - 1;
            sheet.columnOutlines.group(staticCCStartColIndex, chargeCodeEndColumnIndex);
            sheet.columnOutlines.group(chargeCodeStartColIndex, endColIndex);
            const sheetOutlines = this.getColumnOutlineLevels(sheet, rentRollCols.length);
            //Expecting two column outlines for Rent Roll (Multifamily Documnet)
            if (sheetOutlines.length === 2) {
              sheet.columnOutlines.expandGroup(sheetOutlines[1], false);
              sheet.invalidateLayout();
            }
          }
        } else if (this.validateDocumentAssetType(PROPERTY_TYPES.RETAIL.baseClass) || this.validateDocumentAssetType(PROPERTY_TYPES.OFFICE.baseClass)) {
          const rentStepStartIndex = findIndex(rentRollCols, ['type', 'RENT_STEP_DATE']);
          if (rentStepStartIndex !== -1) {
            const endColIndex = rentRollCols.length - rentStepStartIndex;
            sheet.columnOutlines.group(rentStepStartIndex, endColIndex);
            const sheetOutlines = this.getColumnOutlineLevels(sheet, rentRollCols.length);
            if (sheetOutlines.length > 0) {
              sheet.columnOutlines.expandGroup(sheetOutlines[0], false);
              sheet.invalidateLayout();
            }
          }
        }
      }
    }
    this.spreadsheet.resume();
  }
  /** 
  *  Multifamily RentRoll Affix Configurations
  */

  setInitialChargeCode() {
    const { assetType, store } = this.props;
    const chargeCodes = chargeCodeConfigSelector(store.getState());
    const rowCount = this.getTargetSheetRowCount();
    const sheet = this.spreadsheet.getSheet(targetSheetIndex);
    const isRetail = assetType === PROPERTY_TYPES.RETAIL.key;
    const retailUniqeChargeCode = [];
    this.spreadsheet.suspend();
    if (Reflect.ownKeys(chargeCodes).length > 0) {
      Reflect.ownKeys(chargeCodes).forEach((colName) => {
        if (isRetail && isBracketExists(colName)) {
          delete chargeCodes[colName];
          return;
        }
        if (isRetail) {
          const chargeCode = chargeCodes[colName];
          let cellValue = '';
          if (chargeCode) {
            cellValue = chargeCode.label ? chargeCode.label : chargeCode.code ? chargeCode.code : '';
            if (!chargeCode.label) {
              chargeCode.label = chargeCode.code ? !retailUniqeChargeCode.includes(cellValue) ? chargeCode.code : '' : '';
            }
          }

          if (!!cellValue && (!retailUniqeChargeCode.includes(cellValue) || checkArrayIncludesValue(ALLOWED_RETAIL_CC_CATEGORY, cellValue))) {
            retailUniqeChargeCode.push(cellValue);
          } else {
            chargeCode.label ? chargeCode.label = "" : chargeCode.code = "";
            chargeCode.unit = "";
          }
        }
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, colName);
        if (
          foundColumnIndex !== -1) {
          const total = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${this.datasheet.columnAlphabetIndex(foundColumnIndex)}2:${this.datasheet.columnAlphabetIndex(foundColumnIndex)}${rowCount})`);
          if (isNil(chargeCodes[colName].category)) {
            chargeCodes[colName] = {
              total, colIndex: foundColumnIndex, category: chargeCodes[colName], key: colName, ...chargeCodes[colName]
            };
          } else {
            chargeCodes[colName] = {
              ...chargeCodes[colName], colIndex: foundColumnIndex, total, key: colName, ...chargeCodes[colName]
            };
          }
          chargeCodes[colName]['total'] = total;
        }
      });
    }
    this.initailChargeCodes = { ...this.initailChargeCodes, ...chargeCodes };

    store.dispatch(setChargeCodeConfig({
      ...chargeCodes
    }))
    this.spreadsheet.resume();
  }

  retrieveChargeCodes() {
    const { store } = this.props;
    const chargeCodes = chargeCodeConfigSelector(store.getState());
    const rowCount = this.getTargetSheetRowCount();
    const sheet = this.spreadsheet.getSheet(targetSheetIndex);
    this.spreadsheet.suspend();
    if (Reflect.ownKeys(chargeCodes).length > 0) {
      Reflect.ownKeys(chargeCodes).forEach(colName => {
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, colName);
        if (foundColumnIndex !== -1) {
          const total = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${this.datasheet.columnAlphabetIndex(foundColumnIndex)}2:${this.datasheet.columnAlphabetIndex(foundColumnIndex)}${rowCount})`);
          chargeCodes[colName] = {
            ...chargeCodes[colName], total, colIndex: foundColumnIndex, category: chargeCodes[colName].category, key: colName
          };
        }
      });
    }
    store.dispatch(setChargeCodeConfig({
      ...chargeCodes
    }))
    this.spreadsheet.resume();
  }

  getRentRollUniqueColumnValues(columnName) {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      this.spreadsheet.suspend();
      const columnValues = [];
      const rowCount = this.spreadsheet.getSheet(targetSheetIndex).getRowCount();
      const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, columnName);
      for (let i = 1; i < rowCount; i++) {
        const colValue = this.spreadsheet.getSheet(targetSheetIndex).getValue(i, foundColumnIndex);
        !isNil(colValue) && columnValues.push(colValue);
      }
      const result = columnValues.reduce((acc, val) => {
        acc[val] = (acc[val] || 0) + 1;
        return acc;
      }, {});
      this.spreadsheet.resume();
      return result;
    }
  }

  getUniqueFloorPlan() {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const mappedFloorPlan = currentDocumentFloorPlanSelector(store.getState());
      let result = {};
      let finalFloorPlan = {};
      let validColumnName = '';
      let marketRentTotal = 0;
      let marketRentMinValue = 0;
      let marketRentMaxValue = 0;
      let monthlyRentMinValue = 0;
      let monthlyRentMaxValue = 0;
      let monthlyRentTotal = 0;
      let squareFtMinValue = 0;
      let squareFtMaxValue = 0;
      let squareFtTotal = 0;
      let columnValues = [];

      for (let col = 0; col < RENT_ROLL_MF_COLUMN_NAME.length; col++) {
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, RENT_ROLL_MF_COLUMN_NAME[col]);
        if (foundColumnIndex !== -1 && !columnValues.length) {
          const columnData = this.getRentRollUniqueColumnValues(RENT_ROLL_MF_COLUMN_NAME[col]);
          if (!!Reflect.ownKeys(columnData).length) {
            validColumnName = RENT_ROLL_MF_COLUMN_NAME[col];
            columnValues.push(columnData);
            break;
          }
        }
      }

      let columnIndex = 0;
      for (let cellValue of columnValues) {
        columnIndex++;
        if (Reflect.ownKeys(cellValue).length > 0) {
          const isColumnContainsInValidValue = arrayContainsArray(excludedRentRollMFColValue, Reflect.ownKeys(cellValue));
          if (!isColumnContainsInValidValue) {
            result = { ...result, ...cellValue };
          }
        }
      }

      if (Reflect.ownKeys(result).length > 0) {
        Reflect.ownKeys(result).forEach(item => {
          excludedRentRollMFColValue.includes(item) && delete result[item];
        });
        const { rowCount } = this.datasheet.getAlphabetTargetRange(validColumnName, targetSheetIndex);
        const floorPlanColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, validColumnName).foundColumnIndex;
        const marketRentAmoutColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, 'Market Rent').foundColumnIndex;
        const monthlyRentAmoutColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, 'Monthly Rent').foundColumnIndex;
        const squareFtColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, 'Sq Ft').foundColumnIndex;

        finalFloorPlan = Reflect.ownKeys(result).reduce((acc, item) => {
          const bed = !isNil(mappedFloorPlan[item]) ? mappedFloorPlan[item].BD : '';
          const bath = !isNil(mappedFloorPlan[item]) ? mappedFloorPlan[item].BA : '';
          if (marketRentAmoutColumnIndex !== -1) {
            const marketRentColumnValues = this.spreadsheet.getMatchedCellValues(targetSheetIndex, rowCount, floorPlanColumnIndex, marketRentAmoutColumnIndex, item);
            marketRentMinValue = min(compact(marketRentColumnValues)) || 0;
            marketRentMaxValue = max(compact(marketRentColumnValues)) || 0;
            marketRentTotal = sum(compact(marketRentColumnValues)) || 0;
          }
          if (monthlyRentAmoutColumnIndex !== -1) {
            const monthlyRentValues = this.spreadsheet.getMatchedCellValues(targetSheetIndex, rowCount, floorPlanColumnIndex, monthlyRentAmoutColumnIndex, item);
            monthlyRentMinValue = min(compact(monthlyRentValues)) || 0;
            monthlyRentMaxValue = max(compact(monthlyRentValues)) || 0;
            monthlyRentTotal = sum(compact(monthlyRentValues)) || 0;
          }
          if (squareFtColumnIndex !== -1 && floorPlanColumnIndex !== -1) {
            const filteredSqFt = this.spreadsheet.getMatchedCellValues(targetSheetIndex, rowCount, floorPlanColumnIndex, squareFtColumnIndex, item);
            squareFtMinValue = min(compact(filteredSqFt)) || 0;
            squareFtMaxValue = max(compact(filteredSqFt)) || 0;
            squareFtTotal = sum(compact(filteredSqFt)) || 0;
          }
          acc[item] = {
            BD: Number(bed), BA: Number(bath), count: result[item], marketRentTotal, marketRentMinValue, marketRentMaxValue, monthlyRentMinValue, monthlyRentMaxValue, monthlyRentTotal, squareFtMinValue, squareFtMaxValue, squareFtTotal
          };
          return acc;
        }, {});
      }

      store.dispatch(setFloorPlan({
        ...finalFloorPlan
      }));
      this.spreadsheet.resume();
    }
  }


  convertTenantNameToStatus(columnName) {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const columnValues = [];
      let result = {};
      const tenantNameUnitStatusKeys = Reflect.ownKeys(tenantNameUnitStatusSelector(store.getState()));
      if (tenantNameUnitStatusKeys.length) {
        const rowCount = this.spreadsheet.getSheet(targetSheetIndex).getRowCount();
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, columnName);
        for (let i = 1; i < rowCount; i++) {
          const colValue = this.spreadsheet.getSheet(targetSheetIndex).getValue(i, foundColumnIndex);
          if (!isNil(colValue) && !isNil(tenantNameUnitStatusKeys.find(foundValue => colValue.toLowerCase().trim() === foundValue.toLowerCase().trim()))) {
            columnValues.push(colValue);
          } else if (!isNil(colValue)) {
            columnValues.push('Occupied');
          }
        }
        result = columnValues.reduce((acc, val) => {
          acc[val] = (acc[val] || 0) + 1;
          return acc;
        }, {});
      }

      this.spreadsheet.resume();
      return result;
    }
  }

  getUniqueUnitStatus() {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      this.spreadsheet.suspend();
      const { store } = this.props;
      const unitStatusConfig = currentDocumentUnitStatusSelector(store.getState());
      let rowCount; let targetRange; let
        isStatusColumnPresent = true;
      if (Reflect.ownKeys(unitStatusConfig).length > 0) {
        let documentUnitStatus = this.getRentRollUniqueColumnValues('Status');
        if (Reflect.ownKeys(documentUnitStatus).length === 0) {
          documentUnitStatus = this.convertTenantNameToStatus('Tenant Name');
          const columnRange = this.getAlphabetTargetRange('Tenant Name', targetSheetIndex);
          rowCount = columnRange.rowCount;
          targetRange = columnRange.targetRange;
          isStatusColumnPresent = false;
        } else {
          const columnRange = this.datasheet.getAlphabetTargetRange('Status', targetSheetIndex);
          rowCount = columnRange.rowCount;
          targetRange = columnRange.targetRange;
        }

        const sheet = this.spreadsheet.workbook.getSheet(targetSheetIndex);
        const marketRentAmoutColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, 'Market Rent').foundColumnIndex;
        const monthlyRentAmoutColumnIndex = this.datasheet.customSearchColumnWithValue(1, 0, 'Monthly Rent').foundColumnIndex;

        let marketRentTotal = 0;
        let monthlyRentTotal = 0;
        if (!!Reflect.ownKeys(documentUnitStatus) && Reflect.ownKeys(documentUnitStatus).length > 0) {
          const unitStatusConfigKeys = Reflect.ownKeys(unitStatusConfig);
          const documentUnitStatusKeys = Reflect.ownKeys(documentUnitStatus);
          if (documentUnitStatusKeys.length) {
            documentUnitStatusKeys.forEach(unitStatus => {
              const validKeyName = findValidKey(unitStatusConfigKeys, unitStatus);
              if (!isNil(validKeyName)) {
                if (marketRentAmoutColumnIndex !== -1) {
                  const targetSumRange = `${this.datasheet.columnAlphabetIndex(marketRentAmoutColumnIndex)}1:${this.datasheet.columnAlphabetIndex(marketRentAmoutColumnIndex)}${rowCount}`;
                  marketRentTotal = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=ROUND(SUMIF('${TARGET_SHEET_NAME}'!${targetRange},"${validKeyName}",'${TARGET_SHEET_NAME}'!${targetSumRange}),2)`);
                }
                if (monthlyRentAmoutColumnIndex !== -1) {
                  const targetSumRange = `${this.datasheet.columnAlphabetIndex(monthlyRentAmoutColumnIndex)}1:${this.datasheet.columnAlphabetIndex(monthlyRentAmoutColumnIndex)}${rowCount}`;
                  monthlyRentTotal = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=ROUND(SUMIF('${TARGET_SHEET_NAME}'!${targetRange},"${validKeyName}",'${TARGET_SHEET_NAME}'!${targetSumRange}),2)`);
                }
                documentUnitStatus[unitStatus] = {
                  ...unitStatusConfig[validKeyName], marketRentTotal, monthlyRentTotal, count: documentUnitStatus[unitStatus]
                };
              }
            });
          }
        }

        if (!isStatusColumnPresent) {
          if (marketRentAmoutColumnIndex !== -1) {
            const totalMarketRentSumRange = `${this.datasheet.columnAlphabetIndex(marketRentAmoutColumnIndex)}1:${this.datasheet.columnAlphabetIndex(marketRentAmoutColumnIndex)}${rowCount}`;
            const totalMarketRent = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${totalMarketRentSumRange})`);
            const totalOtherMarketRent = Reflect.ownKeys(documentUnitStatus).reduce((acc, curr) => acc = acc + documentUnitStatus[curr].marketRentTotal, 0);
            if (!isNil(documentUnitStatus['Occupied'])) {
              documentUnitStatus['Occupied'].marketRentTotal = totalMarketRent - totalOtherMarketRent;
            }
          }
          if (monthlyRentAmoutColumnIndex !== -1) {
            const totalMonthlyRentSumRange = `${this.datasheet.columnAlphabetIndex(monthlyRentAmoutColumnIndex)}1:${this.datasheet.columnAlphabetIndex(monthlyRentAmoutColumnIndex)}${rowCount}`;
            const totalMonthlyRent = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${totalMonthlyRentSumRange})`);
            const totalOtherMonthlyRent = Reflect.ownKeys(documentUnitStatus).reduce((acc, curr) => acc = acc + documentUnitStatus[curr].monthlyRentTotal, 0);
            if (!isNil(documentUnitStatus['Occupied'])) {
              documentUnitStatus['Occupied'].monthlyRentTotal = totalMonthlyRent - totalOtherMonthlyRent;
            }
          }
        }
        store.dispatch(setUnitStatus({
          ...documentUnitStatus
        }));
      }
      this.spreadsheet.resume();
    }
  }

  mergeChargeCodeColumns(chargeCodeColumnRef) {
    if (Reflect.ownKeys(chargeCodeColumnRef).length > 0) {
      const { sourceColumnRef, targetcolumnRef } = chargeCodeColumnRef;
      this.spreadsheet.suspend();
      const rowCount = this.getTargetSheetRowCount();
      const sheet = this.spreadsheet.getSheet(targetSheetIndex);
      const sourceColCellIndex = this.datasheet.customSearchColumnWithValue(1, 0, sourceColumnRef.key).foundColumnIndex;
      const targetColCellIndex = this.datasheet.customSearchColumnWithValue(1, 0, targetcolumnRef.key).foundColumnIndex;
      for (let row = 1; row < rowCount; row++) {
        const sourceColValue = this.spreadsheet.getCellValue(targetSheetIndex, row, sourceColCellIndex);
        const targetColValue = this.spreadsheet.getCellValue(targetSheetIndex, row, targetColCellIndex);
        const total = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${isNil(sourceColValue) ? 0 : sourceColValue}+${isNil(targetColValue) ? 0 : targetColValue})`);
        this.spreadsheet.setCellValue(targetSheetIndex, row, targetColCellIndex, total);
      }
      sheet.deleteColumns(sourceColCellIndex, 1);
      this.spreadsheet.bindSheetHeader(sheet);
      this.deleteChargeCodeColumnFromStore(sourceColumnRef.key);
      if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
        this.setChargeCodeAfterColumnMerge();
      } else {
        this.setRetailChargeCode();
      }
    }
    this.spreadsheet.resume();
  }

  deleteChargeCodeColumnFromStore(keyName) {
    const { store } = this.props;
    const chargeCode = chargeCodeConfigSelector(store.getState());
    delete chargeCode[keyName];
    store.dispatch(setChargeCodeConfig({
      ...chargeCode
    }));
  }

  setChargeCodeAfterColumnMerge(isSlidePanelClicked = false) {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      const userSelectedChargeCode = chargeCodeConfigSelector(store.getState());
      const chargeCodes = invertBy(Reflect.ownKeys(userSelectedChargeCode).reduce((chargeCode, chargeCodeKey) => {
        chargeCode[chargeCodeKey] = userSelectedChargeCode[chargeCodeKey].category;
        return chargeCode;
      }, {}));
      const rowCount = this.getTargetSheetRowCount();
      const sheet = this.spreadsheet.getSheet(targetSheetIndex);
      const chargeCodeKeys = Reflect.ownKeys(chargeCodes);
      if (chargeCodeKeys.length > 0) {
        this.spreadsheet.suspend();
        this.clearChargeCodeCategory(rowCount, sheet);
        for (let targetKey in chargeCodes) {
          const targetFormulaCellIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, targetKey).foundColumnIndex;
          let formulaExpression = '';
          chargeCodes[targetKey].forEach(chargeCode => {
            const chargeCodeColIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, chargeCode).foundColumnIndex;
            if (chargeCodes[targetKey].length > 1) {
              if (formulaExpression === '') {
                formulaExpression += `${this.datasheet.columnAlphabetIndex(chargeCodeColIndex)}&`;
              } else {
                formulaExpression += `+${this.datasheet.columnAlphabetIndex(chargeCodeColIndex)}&`;
              }
            } else {
              formulaExpression += `${this.datasheet.columnAlphabetIndex(chargeCodeColIndex)}&`;
            }
          });
          if (targetFormulaCellIndex !== -1) {
            for (let row = 1; row < rowCount; row++) {
              const finalFormulaExpression = formulaExpression.split("&").join(row + 1);
              const cellValue = sheet.getCell(row, targetFormulaCellIndex).value();
              if (cellValue === null || !cellValue) {
                sheet.setFormula(row, targetFormulaCellIndex, `=SUM(${finalFormulaExpression})`);
              }
            }
          }
        }

        if (!isSlidePanelClicked) {
          this.retrieveChargeCodes();
          this.props.setWorkbookToast({ type: 'success', message: 'Charge code successfully merged.' });
        }

        setTimeout(() => {
          this.getUniqueUnitStatus();
          this.getUniqueFloorPlan();
        }, 0);
        this.spreadsheet.resume();
      }
    }
  }

  clearChargeCodeCategory(rowCount, sheet) {
    this.spreadsheet.suspend();
    const { store } = this.props;
    const chargeCodes = staticChargeCodeSelector(store.getState());
    if (chargeCodes.length > 0) {
      for (let chargeCodeColumnIndex = 0; chargeCodeColumnIndex < chargeCodes.length; chargeCodeColumnIndex++) {
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, chargeCodes[chargeCodeColumnIndex]);
        if (foundColumnIndex !== -1) {
          const range = sheet.getRange(1, foundColumnIndex, rowCount, 1);
          range.formatter(cellNumberFormat);
          for (let row = 1; row < rowCount; row++) {
            const cellValue = sheet.getCell(row, foundColumnIndex).value();
            const isFormulaExist = sheet.getFormula(row, foundColumnIndex);
            if (!isNil(isFormulaExist) && (!!cellValue || cellValue === 0)) {
              sheet.setFormula(row, foundColumnIndex, null);
              sheet.setText(row, foundColumnIndex, null);
            }
          }
        }
      }
    }
    this.spreadsheet.resume();
  }


  getLeaseInfoFromDocument(columnNames, sheetIndex, rowCount) {
    this.spreadsheet.suspend();
    const columnValues = [];
    for (let headerCol = 0; headerCol < columnNames.length; headerCol++) {
      const columnIndex = this.datasheet.customSearchColumnWithValue(sheetIndex, 0, columnNames[headerCol]).foundColumnIndex;
      if (columnIndex !== -1) {
        for (let row = 1; row < rowCount; row++) {
          const cellValue = this.spreadsheet.getCellValue(targetSheetIndex, row, columnIndex);
          if (!isNil(cellValue)) {
            headerCol === 0 ? columnValues.push(cellValue) : !!cellValue && (columnValues[row - 1] = cellValue);
          }
        }
      }
    }
    const dates = columnValues.filter(colValue => moment(colValue, "MM/DD/YYYY")._isValid && colValue);
    const sortedDate = sortBy(dates, (date) => moment(date, 'MM/DD/YYYY').format('X'));
    const groupedDate = groupBy(sortedDate, (date) => moment(date, 'MM/DD/YYYY').format('MMM-YY'));
    const monthWiseCount = Reflect.ownKeys(groupedDate)
      .map(month => ({
        "name": month, 'Lease Count': groupedDate[month].length
      }));

    const yearWiseCount = groupBy(monthWiseCount, (countData) => moment(countData.name, 'MMM-YY').format('YYYY'));
    this.spreadsheet.resume();
    return yearWiseCount;
  }

  setRentRollLeaseTypeData() {
    if (this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const rowCount = this.spreadsheet.getSheet(targetSheetIndex).getRowCount();
      const leaseStartRange = this.getLeaseInfoFromDocument(['Start Date', 'Move In Date'], targetSheetIndex, rowCount);
      const leaseExpirtyRange = this.getLeaseInfoFromDocument(['End Date', 'Move Out Date'], targetSheetIndex, rowCount);
      store.dispatch(setLeaseConfig(
        leaseStartRange, leaseExpirtyRange
      ));
      this.spreadsheet.resume();
    }
  }

  /*****
   * Retail and other type docuemnts except Multifamily
   */

  getRetailLeaseExpirationData() {
    if (!this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const columnValues = this.datasheet.fetchValueFromColumns(targetSheetIndex, ['End Date', 'Sq Ft'], ['endDate', 'sqFt']);
      const groupedDates = groupBy(columnValues, 'endDate');
      const monthWiseCount = Reflect.ownKeys(groupedDates).map(date => ({ name: date, total: sumBy(groupedDates[date], 'sqFt') }));
      const yearWiseCount = groupBy(monthWiseCount, (countData) => moment(countData.name, "MM/DD/YYYY")._isValid && moment(countData.name, 'MM-DD-YYYY').format('YYYY'));
      const finalLeaseExpiration = Reflect.ownKeys(yearWiseCount).map(year => ({ 'name': year, 'SF Rolling': sumBy(yearWiseCount[year], 'total') })).filter(item => item.name !== 'false' && item);
      store.dispatch(setLeaseConfig(
        null, finalLeaseExpiration
      ));
      this.spreadsheet.resume();
    }
  }

  getRetailOccupancyStatus() {
    if (!this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const projectOccupancy = occupancySelector(store.getState());
      const projectOccupancyMappings = occupancyMappingSelector(
        store.getState()
      );
      const columnValues = this.datasheet.fetchValueFromColumns(targetSheetIndex, ['Status', 'Sq Ft'], ['status', 'sqFt']).filter(key => key.status);
      const groupedStatus = groupBy(columnValues, 'status');
      const filterprojectOccupancy = Array.isArray(projectOccupancy) ? groupBy(projectOccupancy, 'name') : [];
      const finalOccupancyData = Reflect.ownKeys(groupedStatus).map(status => {
        const category = !isNil(filterprojectOccupancy[status]) ? filterprojectOccupancy[status][0].category : this.getOccupancyCategory(status, projectOccupancyMappings);
        return {
          name: status, units: groupedStatus[status].length, category, value: sumBy(groupedStatus[status], 'sqFt')
        };
      });
      store.dispatch(setRetailOccupancy(
        finalOccupancyData
      ));
      this.spreadsheet.resume();
    }
  }

  getOccupancyCategory(status, projectOccupancyMappings) {
    return projectOccupancyMappings.includes(status) ? status : "";
  }

  getRetailLeaseType() {
    if (!this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const { store } = this.props;
      this.spreadsheet.suspend();
      const rowCount = this.spreadsheet.getSheet(targetSheetIndex).getRowCount();
      const leaseTypeMapping = retailLeaseTypeDropDownSelector(store.getState());
      const leaseTypeConfig = retailLeaseTypeConfigSelector(store.getState());
      const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, 'Lease Type');
      if (foundColumnIndex !== -1) {
        const leaseTypeColumnValues = uniq(this.spreadsheet.getColumnValues(targetSheetIndex, 1, rowCount, foundColumnIndex).filter(leaseType => leaseType !== null && leaseType));
        const uniqueColumnValue = difference(leaseTypeColumnValues, leaseTypeMapping);
        const finalLeaseType = uniqueColumnValue.length ? Object.assign(...uniqueColumnValue.map(item => ({ [item]: !isNil(leaseTypeConfig[item]) ? leaseTypeConfig[item] : '' }))) : {};
        store.dispatch(setRetailLeaseTypeConfig(
          finalLeaseType
        ));
      }
      this.spreadsheet.resume();
    }
  }


  getRetailTenantTypeData() {
    if (!this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      this.spreadsheet.suspend();
      const { store } = this.props;
      const columnValues = this.datasheet.fetchValueFromColumns(targetSheetIndex, Object.values(tenantTypeColumnCalc), Reflect.ownKeys(tenantTypeColumnCalc)).filter(tenant => tenant.TENANT_TYPE);
      const totalSquareFt = sumBy(columnValues, getKeyByValue(tenantTypeColumnCalc, tenantTypeColumnCalc.SQFT));
      const groupedByTenantName = groupBy(columnValues, getKeyByValue(tenantTypeColumnCalc, tenantTypeColumnCalc.TENANT_TYPE));
      const finalTenantTypeData = Reflect.ownKeys(groupedByTenantName).map(tenant => {
        if (!isNil(tenant)) {
          const sqFtTotal = sumBy(groupedByTenantName[tenant], 'SQFT');
          const totalRent = some(groupedByTenantName[tenant], 'BASE_RENT') ? getTotalByKey(groupedByTenantName[tenant], 'BASE_RENT') :
            sumBy(groupedByTenantName[tenant], key => !!key.MONTLY_RENT_CALC ? Number(key.MONTLY_RENT_CALC) : 0);

          const MONTLY_CAM = some(groupedByTenantName[tenant], 'CAM_RE') ? getTotalByKey(groupedByTenantName[tenant], 'CAM_RE') :
            sumBy(groupedByTenantName[tenant], key => !!key.MONTLY_CAM_CALC ? Number(key.MONTLY_CAM_CALC) : 0);

          const MONTLY_TAX = some(groupedByTenantName[tenant], 'TAX_RE') ? getTotalByKey(groupedByTenantName[tenant], 'TAX_RE') :
            sumBy(groupedByTenantName[tenant], key => !!key.MONTLY_TAX_CALC ? Number(key.MONTLY_TAX_CALC) : 0);

          const MONTLY_INC = some(groupedByTenantName[tenant], 'INS_RE') ? getTotalByKey(groupedByTenantName[tenant], 'INS_RE') :
            sumBy(groupedByTenantName[tenant], key => !!key.MONTLY_INC_CALC ? Number(key.MONTLY_INC_CALC) : 0);

          const totalSqFtPercent = getPercentage(sqFtTotal, totalSquareFt);
          const totalRecovery = Number(MONTLY_CAM + MONTLY_TAX + MONTLY_INC);
          return {
            name: tenant, Recoveries: totalRecovery, sqFtTotal, Rent: isNaN(totalRent) ? 0 : totalRent, totalSqFtPercent
          };
        }
      });

      store.dispatch(setRetailTenantTypeConfig(
        finalTenantTypeData
      ));

      this.spreadsheet.resume();
    }
  }

  calculateTenantType(calcultedPercent) {
    this.spreadsheet.suspend();
    const columnValues = this.datasheet.fetchValueFromColumns(targetSheetIndex, [tenantTypeColumnCalc.TENANT_TYPE, tenantTypeColumnCalc.SQFT], ['TENANT_TYPE', 'SQFT']);
    const totalSquareFt = sumBy(columnValues, 'SQFT');
    const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, 'Tenant Type');
    if (!!columnValues.length && foundColumnIndex !== -1) {
      if (foundColumnIndex) {
        for (let row = 0; row < columnValues.length; row++) {
          const tenantType = this.getTenantType(
            columnValues[row].SQFT,
            totalSquareFt,
            calcultedPercent
          );
          this.spreadsheet.setCellValue(targetSheetIndex, columnValues[row].TENANT_TYPE_ROW_INDEX, foundColumnIndex, tenantType);
        }
      }
    }
    this.getRetailTenantTypeData();
    this.spreadsheet.resume();
  }

  getTenantType(sqft, totalSquareFt, calcultedPercent) {
    const { assetType } = this.props;
    const inlineType = assetType === PROPERTY_TYPES.OFFICE.key ? 'Minor' : 'Inline';
    const tenantType =
      getPercentage(sqft, totalSquareFt) >= calcultedPercent ?
        "Major" :
        inlineType;

    return tenantType;
  }

  setRetailChargeCode() {
    this.spreadsheet.suspend();
    if (!this.validateDocumentAssetType(PROPERTY_TYPES.MULTIFAMILY.baseClass)) {
      const {store} = this.props;
      const userSelectedChargeCode = chargeCodeConfigSelector(store.getState());
      const rowCount = this.getTargetSheetRowCount();
      const sheet = this.spreadsheet.workbook.getSheet(targetSheetIndex);
      const squareFtColumnIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, 'Sq Ft').foundColumnIndex;
      const chargeCodesColumns = staticChargeCodeSelector(store.getState()) || [];
      const staticChargeCodeMappings = staticChargeCodeMappingSelector(store.getState());
      const annualCalcChargeCodeColumnMappings = staticChargeCodeMappings.map(chargeCode => chargeCode.mappedCalculatedColumn).filter(cc => cc);
      const chargeCodeColumnsToClear = [...chargeCodesColumns, ...annualCalcChargeCodeColumnMappings].filter(cc => !!cc);
      this.clearRetailChargeCodeColumns(sheet, chargeCodeColumnsToClear, rowCount);
      const otherRecoveries = [];
      for (let targetKey in userSelectedChargeCode) {
        const { label, unit } = userSelectedChargeCode[targetKey];
        const sourceColCellIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, targetKey).foundColumnIndex;
        const targetColCellIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, label).foundColumnIndex;
        let mappedCalcColIndex = -1;
        const findMappedColumnName = !!staticChargeCodeMappings && staticChargeCodeMappings.find(mappedCol => mappedCol.columnName === label);
        if (!isNil(findMappedColumnName)) {
          mappedCalcColIndex = this.datasheet.customSearchColumnWithValue(targetSheetIndex, 0, findMappedColumnName.mappedCalculatedColumn).foundColumnIndex;
        }
        if (userSelectedChargeCode[targetKey] && !!label) {
          if (targetColCellIndex !== -1 && sourceColCellIndex !== -1) {
            for (let row = 1; row < rowCount; row++) {
              const sourceColValue = this.spreadsheet.getCellValue(targetSheetIndex, row, sourceColCellIndex);
              const sqFtColumnValue = squareFtColumnIndex !== -1 ? this.spreadsheet.getCellValue(targetSheetIndex, row, squareFtColumnIndex) : 1;
              let calculatedCellValue = this.calculateRetailChargeCodeTotal(sourceColValue, unit, sqFtColumnValue) || 0;
              if (label === ALLOWED_RETAIL_CC_CATEGORY[0]) { // Expecting Other Recoveries here
                calculatedCellValue = sum([otherRecoveries[row], calculatedCellValue]);
                if (!isNil(otherRecoveries[row])) {
                  otherRecoveries.push(calculatedCellValue);
                } else {
                  otherRecoveries[row] = calculatedCellValue;
                }
              }

              this.spreadsheet.setCellValue(targetSheetIndex, row, targetColCellIndex, calculatedCellValue);
              mappedCalcColIndex !== -1 && this.spreadsheet.setCellFormula(sheet, row, mappedCalcColIndex, `=PRODUCT(${this.datasheet.columnAlphabetIndex(targetColCellIndex)}${row + 1}, 12)`); // (chargeCode*12) To get annual total
            }
            const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, targetKey);
            if (foundColumnIndex !== -1) {
              const total = GC.Spread.Sheets.CalcEngine.evaluateFormula(sheet, `=SUM(${this.datasheet.columnAlphabetIndex(foundColumnIndex)}2:${this.datasheet.columnAlphabetIndex(foundColumnIndex)}${rowCount})`);
              userSelectedChargeCode[targetKey]['total'] = total;
            }
          }
        }
      }
    }
    this.spreadsheet.resume();
  }

  calculateRetailChargeCodeTotal(chargeCodeTotal, unit, sqFtColumnValue) {
    this.spreadsheet.suspend();
    const {
      EMPTY, MONTHLY, ANNUAL, ANNUAL_PER_SQFT, MONTHLY_PER_SQFT
    } = RETAIL_CHARGE_CODE_FORMULA;
    let calculatedCellValue = 0;
    if (chargeCodeTotal !== null) {
      if (unit === EMPTY || unit === MONTHLY || isNil(unit)) {
        calculatedCellValue = chargeCodeTotal; // Show Charge Code Value as it is
      }
      if (unit === ANNUAL) {
        calculatedCellValue = divide(chargeCodeTotal, 12); // [chargeCode/12]
      }
      if (unit === ANNUAL_PER_SQFT) {
        calculatedCellValue = multiply(divide(chargeCodeTotal, 12), sqFtColumnValue); // [chargeCode/12*SqFt]
      }
      if (unit === MONTHLY_PER_SQFT) {
        calculatedCellValue = multiply(chargeCodeTotal, sqFtColumnValue); // [chargeCode*SqFt]
      }
    }
    this.spreadsheet.resume();
    return calculatedCellValue;
  }

  clearRetailChargeCodeColumns(sheet, chargeCodesColumns, rowCount) {
    this.spreadsheet.suspend();
    if (chargeCodesColumns.length > 0) {
      for (let chargeCodeColumnIndex = 0; chargeCodeColumnIndex < chargeCodesColumns.length; chargeCodeColumnIndex++) {
        const { foundColumnIndex } = this.datasheet.customSearchColumnWithValue(1, 0, chargeCodesColumns[chargeCodeColumnIndex]);
        if (foundColumnIndex !== -1) {
          const range = sheet.getRange(1, foundColumnIndex, rowCount, 1);
          range.formatter(cellNumberFormat);
          for (let row = 1; row < rowCount; row++) {
            sheet.setFormula(row, foundColumnIndex, null);
            sheet.setText(row, foundColumnIndex, null);
          }
        }
      }
    }
    this.spreadsheet.resume();
  }



  render() {
    const { className, style, taggingInfo, onSlidePaneOpen, onRefereceWindowOpen, document, sheetCount, showToolbar } = this.props;
    return (
      <>
        <div className={`${className} vertical-section`} style={style}>
          <DataSpreadSheet
            toolbarCustomRightChild={(
              <>
                <div className={`${coreStyle.spreadsheetSideToolbar} border-0`}>
                  <DocExtractionTaggingInfo docType={DocumentFileTypes.RENT_ROLL.key} taggingInfo={taggingInfo} />
                  <div className={`position-relative d-inline-block ${coreStyle.spreadsheetToolbarSeparator}`} aria-disabled="true" role="separator line"> &nbsp; </div>
                  <SpreadSheetToolbarButton title="Download Workbook" size="lg" className={coreStyle.downloadFileIcon} onClick={() => this.datasheet.downloadXLS(document)} />
                  <SpreadSheetToolbarButton title="Save Workbook" size="lg" className={coreStyle.saveFileIcon} onClick={() => this.onSaveWorkbook()} />
                  <SpreadSheetToolbarButton title="Open Reference Window" size="lg" className={coreStyle.referenceWindowIcon} onClick={onRefereceWindowOpen} />
                  <SpreadSheetToolbarButton title="Keyboard Shortcuts" size="lg" className={coreStyle.keyboardShortcutsIcon} onClick={() => onSlidePaneOpen(affixComponentName.SHORTCUT_KEYS, affixComponentName.SHORTCUT_KEYS)} />
                </div>

              </>
            )}
            onReady={this.loadData}
            ref={this.refDataSheet}
            showToolbar = {showToolbar}
            sheetCount={sheetCount} />
        </div>
      </>
    );
  }
}

RRSpreadSheet.propTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  assetType: PropTypes.string,
  toolbarCustomRightChild: PropTypes.node,
  workbookData: PropTypes.object,
  taggingInfo: PropTypes.object,
  onSlidePaneOpen: PropTypes.func,
  onRefereceWindowOpen: PropTypes.func,
  data: PropTypes.shape({
    meta: PropTypes.object.isRequired,
    source: PropTypes.object.isRequired,
    target: PropTypes.shape({
      rows: PropTypes.array.isRequired,
      columns: PropTypes.array.isRequired,
    }),
  }),
  sheetCount: PropTypes.number,
  showToolbar: PropTypes.bool
};

export default connect(null)(withServices('store')(RRSpreadSheet));
