import React, {useMemo, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {isNil} from 'lodash';
import {faEllipsisV} from '@fortawesome/free-solid-svg-icons/faEllipsisV';
import * as moment from 'moment';
import {Dropdown} from 'react-bootstrap';
import DocumentsService from '../../../services/DocumentsService';
import {ExtractionJobStatus, DocumentFileTypes as DOC_TYPES, FILE_TYPES} from '../../../constants';
import ItemWithContextMenu from '../ItemTile/ItemWithContextMenu';
import IconButton from '../../core/Button/IconButton';
import style from './DocumentsListView.module.scss';
import DocumentContextMenu from '../DocumentContextMenu/DocumentContextMenu';
import DocumentList from './DocumentList';
import {doSorting, getIsDocumentReExtractRequired, isOSBaseType} from '../../../utils/utils';
import {currentProjectSelector, userAllowedTemplatesSelector} from '../../../store/selectors';
import './DocumentsListView.scss';
import rightArrow from '../../../assets/images/right-arrow.svg';
import {SpriteExtractedIcon, SpriteDeleteIcon, SpriteNeedSupportIcon} from '../../core/SpriteIcon/SpriteIcon';
import useExpandRow from '../../../hooks/useExpandRow';
import ProTag from '../ProTag/ProTag';

const extractionStatusMap = {
  Default: 0,
  Successful: 1,
  Active: 2,
  Canceled: 3,
  Pending: 4,
  Converted: 5,
  Failed: 6
};

const getDocument = (document) => (document.children ? document.children[0] : document);
function ChildDocumentContextMenu({
  document, onNeedSupport, hideContextMenu, onDelete, onOpen, onReExtract
}) {
  const isReExtract = useMemo(() => getIsDocumentReExtractRequired(document), [document]);

  const openOrReExtractAction = useCallback((document) => {
    const action = isReExtract ? onReExtract : onOpen;
    action(document);
  }, [isReExtract]);

  return (
    <div>
      <Dropdown.Menu show>
        <Dropdown.Item
          onClick={() => hideContextMenu() & openOrReExtractAction(document)}
        >
          <SpriteExtractedIcon />
          {`${isReExtract ? "Re Extract" : "View Extracted Data"}`}
        </Dropdown.Item>
        <Dropdown.Item onClick={() => hideContextMenu() & onDelete()}>
          <SpriteDeleteIcon />
          Delete
        </Dropdown.Item>
        <Dropdown.Item
          className="position-relative"
          onClick={() => hideContextMenu() & onNeedSupport()}
        >
          <SpriteNeedSupportIcon />
          Need Support
          <ProTag className="needSupport" />
        </Dropdown.Item>
      </Dropdown.Menu>
    </div>
  );
}


const getDocumentType = (document) => {
  if (document.children) {
    return [...new Set(document.children.map(document => getDocumentType(document)))].join('/');
  }
  const documentType = document.taggingData &&
    document.taggingData.documentType ?
    document.taggingData.documentType :
    DOC_TYPES.NONE.key;
  const assetTypeBadge = documentType !== DOC_TYPES.NONE.key ? DOC_TYPES[documentType].badgeText : '';
  return assetTypeBadge;
};

const getDate = (document) => {
  if (document.taggingData) {
    const {documentType} = document.taggingData;
    if (!isNil(documentType) && isOSBaseType(documentType)) {
      return moment(new Date(document.taggingData.periodFrom));
    } else if (documentType === DOC_TYPES.RENT_ROLL.key) {
      return moment(new Date(document.taggingData.asOnDate));
    } else {
      return null;
    }
  }
};

const fileTypeIcon = (document) => {
  const docType = DocumentsService.getDocumentType(document);
  const icon = FILE_TYPES[docType] ? FILE_TYPES[docType].icon : '';
  return <img src={icon} width={30} height={30} alt="file icon" />;
};

const fileTypeFormatter = (_, document) => {
  if (!document.groupId) {
    return fileTypeIcon(document);
  }
};

const documentTypeSorter = (document1, document2, order) => {
  const assetType1 = getDocumentType(getDocument(document1));
  const assetType2 = getDocumentType(getDocument(document2));
  return doSorting(assetType1, assetType2, order);
};

const taggedDateSorter = (document1, document2, order) => {
  const date1 = getDate(getDocument(document1));
  const date2 = getDate(getDocument(document2));
  return doSorting(date2, date1, order);
};

const getDocumentExtractionStatus = (document) => {
  let statusStyle = ExtractionJobStatus['DEFAULT'];
  if (document.extractionJob && document.extractionJob.status) {
    statusStyle = ExtractionJobStatus[document.extractionJob.status];
  }
  return statusStyle;
};

const getDocumentsChildExtractionStatus = ({children}) => {
  const statusStyleMap = {key: 0, value: ExtractionJobStatus['DEFAULT']};
  // eslint-disable-next-line no-restricted-syntax
  for (const document of children) {
    const statusStyle = getDocumentExtractionStatus(document);
    if (statusStyleMap.key < extractionStatusMap[statusStyle.label]) {
      statusStyleMap['key'] = extractionStatusMap[statusStyle.label];
      statusStyleMap['value'] = statusStyle;
    }
  }

  return statusStyleMap['value'];
};

const getDocumentDocumentExtractionStatus = (document) => {
  if (document.children) {
    return getDocumentsChildExtractionStatus(document).icon;
  }
  let extractionIcon;
  if (document.extractionJob && document.extractionJob.status) {
    extractionIcon = ExtractionJobStatus[document.extractionJob.status].icon;
  }
  return extractionIcon;
};

const documentTypeFormatter = (_, document) => (
  <div title={document.extractionJob && document.extractionJob.status}>
    {getDocumentDocumentExtractionStatus(document)}
    {' '}
    {getDocumentType(document)}
  </div>
);

const getDocumentStatusStyle = (document) => {
  if (document.children) {
    return style[getDocumentsChildExtractionStatus(document).label];
  }
  const statusStyle = getDocumentExtractionStatus(document).label;
  return style[statusStyle];
};

const taggedDateFormatter = (_, document) => {
  if (document.children && document.children.length === 1) {
    return taggedDateFormatter(null, document.children[0]);
  }
  const rrDateFormat = 'MMM DD, YYYY';
  const osDateFormat = 'MMM, YYYY';
  if (document.taggingData) {
    const {documentType} = document.taggingData;
    if (!isNil(documentType) && isOSBaseType(documentType)) {
      return `${moment(new Date(document.taggingData.periodFrom)).utc().format(osDateFormat)
      } - ${
        moment(new Date(document.taggingData.periodTo)).utc().format(osDateFormat)}`;
    } else if (documentType === DOC_TYPES.RENT_ROLL.key) {
      return moment(new Date(document.taggingData.asOnDate)).utc().format(rrDateFormat);
    } else {
      return null;
    }
  }
  return null;
};

const DocumentsListView = ({
  documents,
  project,
  templates,
  onNeedSupport,
  onClearTagging,
  onDelete,
  onAddTagging,
  onDownloadCSV,
  onOpen,
  onReExtract,
  onRename,
  selectDocument,
  selectedDocument,
  goToAddTaggingStep, hasTrialExhausted
}) => {
  const {
    isExpanded, expandDocument, collapseDocument, toggleCollapse
  } = useExpandRow();

  const parentDocumentActionFormatter = (document, rowIndex) => (
    <div className="ml-auto docContextMenuDropdown">
      <ItemWithContextMenu
        openOnClick
        afterShowContextMenu={goToAddTaggingStep}
        TileComponent={() => (
          <IconButton
            id={`actionButton-${rowIndex}`}
            variant="outline-secondary"
            className="smallIconButton"
            icon={faEllipsisV}
          />
        )}
        ContextMenuComponent={({hideContextMenu}) => (
          <DocumentContextMenu
            document={document}
            template={templates[project.templateUuid]}
            hideContextMenu={hideContextMenu}
            onReExtract={onReExtract}
            onDownloadCSV={() => onDownloadCSV(document)}
            onAddTagging={() => onAddTagging(document)}
            onDelete={() => onDelete(document)}
            onNeedSupport={() => onNeedSupport(document)}
            hasTrialExhausted={hasTrialExhausted}
            onClearTagging={() => onClearTagging(document)}
            onOpen={onOpen}
            onRename={() => onRename(document)}
          />
        )}
      />
    </div>
  );

  const childDocumentActionFormatter = useCallback((document) => (
    <div className="ml-auto docContextMenuDropdown">
      <ItemWithContextMenu
        openOnClick
        TileComponent={() => (
          <IconButton
            variant="outline-secondary"
            className="smallIconButton"
            icon={faEllipsisV}
          />
        )}
        ContextMenuComponent={({hideContextMenu}) => (
          <ChildDocumentContextMenu
            document={document}
            template={templates[project.templateUuid]}
            onReExtract={onReExtract}
            hideContextMenu={hideContextMenu}
            onDelete={() => onDelete(document)}
            onNeedSupport={() => onNeedSupport(document)}
            onOpen={onOpen}
          />
        )}
      />
    </div>
  ), []);

  const documentActionFormatter = useCallback((_, document, rowIndex) => (!document.groupId ?
    parentDocumentActionFormatter(document, rowIndex) :
    childDocumentActionFormatter(document)), []);

  const expandIconFormatter = useCallback((_, document) => {
    if (!!document.children && document.children.length > 1) {
      return isExpanded(document) ? (
        <img
          src={rightArrow} className={`${style.arrowDown} ${style.arrowWidth}`} alt="arrow down" />
      ) : (
        <img
          src={rightArrow} className={`${style.arrowLeft} ${style.arrowWidth}`} alt="arrow left" />
      );
    }
    return false;
  }, [isExpanded, expandDocument, collapseDocument]);

  const columns = useMemo(() => [
    {
      dataField: 'expand-icon',
      label: '',
      width: '50px',
      columnClassName: style.expandArrowIcon,
      dataFormat: expandIconFormatter
    },
    {
      dataField: 'documentType',
      label: '',
      width: '50px',
      columnClassName: style.folderIcon,
      dataFormat: fileTypeFormatter
    },
    {
      dataField: 'fileName',
      label: 'Name',
      columnClassName: 'text-with-ellipsis',
      dataFormat: (_, document) => !document.groupId && <span title={document['fileName']}>{document['fileName']}</span>,
      dataSort: true
    },
    {
      dataField: 'documentType',
      label: 'Type',
      width: '80px',
      headerClassName: 'ml-auto',
      columnClassName: (_, document) => `ml-auto ${style.badgeStatus} ${getDocumentStatusStyle(document)}`,
      dataFormat: documentTypeFormatter,
      dataSort: true,
      sortFunc: documentTypeSorter
    },
    {
      dataField: 'taggedDate',
      label: 'Period',
      width: '150px',
      headerClassName: 'ml-auto',
      columnClassName: 'ml-auto',
      dataFormat: taggedDateFormatter,
      dataSort: true,
      sortFunc: taggedDateSorter
    },
    {
      dataField: 'actions',
      label: 'Actions',
      width: '60px',
      headerClassName: 'ml-auto',
      columnClassName: 'ml-auto',
      dataFormat: documentActionFormatter
    }
  ]);

  return (
    <DocumentList
      columns={columns}
      data={documents}
      selectedItem={selectedDocument}
      onSelect={selectDocument}
      isExpanded={isExpanded}
      toggleCollapse={toggleCollapse}
    />
  );
};
const mapStateToProps = (state) => ({
  project: currentProjectSelector(state),
  templates: userAllowedTemplatesSelector(state)
});

export default connect(mapStateToProps)(DocumentsListView);
