import React, {useMemo, useCallback} from 'react';
import Dropdown from 'react-bootstrap/Dropdown';
import {getIsDocumentReExtractRequired} from '../../../utils/utils';
import {
  SpriteExtractedIcon, SpriteAddMoreTagsIcon, SpriteRenameIcon,
  SpriteClearTaggingIcon, SpriteDeleteIcon, SpriteNeedSupportIcon
} from '../../core/SpriteIcon/SpriteIcon';
import ProTag from '../ProTag/ProTag';

function DocumentContextMenu({
  document, template, onRename, onNeedSupport, hideContextMenu, onDelete, onOpen,
  onClearTagging, onAddTagging, onReExtract, onDownloadCSV, hasTrialExhausted
}) {
  const isMaskingTemplate = template.keysToMask.length > 0;
  const disableClearTagging = useMemo(() => {
    if (!!document.children || isMaskingTemplate) {
      return false;
    } else {
      return !document.taggingData || !document.taggingData.documentType;
    }
  }, [document]);

  const isReExtract = useCallback((document) => getIsDocumentReExtractRequired(
    document, isMaskingTemplate
  ), [isMaskingTemplate]);

  const openOrReExtractAction = useCallback((document) => {
    const action = isReExtract(document) ? onReExtract : onOpen;
    action(document);
  }, []);

  const canAddTag = useCallback((document) => (
    !document.children &&
    !(document.taggingData && document.taggingData.documentType) ?
      true :
      !isMaskingTemplate
  ), []);
  const canViewExtracted = useCallback((document) => !!(document.children && document.children.length === 1), []);
  const canOldViewExtracted = useCallback((document) => !document.children &&
  !!(document.taggingData && document.taggingData.documentType), []);
  const canClearTagging = useCallback((document) => !isMaskingTemplate, []);
  const isAddMoreTag = useMemo(() => (
    document.taggingData &&
    document.taggingData.documentType
  ) ||
  document.children, [document]);

  return (
    <div id="DocumentContextMenu">
      <Dropdown.Menu show>
        {canViewExtracted(document) && (
          <Dropdown.Item
            onClick={() => hideContextMenu() & openOrReExtractAction(document.children[0])
            }
          >
            <SpriteExtractedIcon />
            {" "}
            {`${
              isReExtract(document.children[0]) ?
                "Re Extract" :
                "View Extracted Data"
            }`}
            {" "}
          </Dropdown.Item>
        )}
        {canOldViewExtracted(document) && (
          <Dropdown.Item
            onClick={() => hideContextMenu() & openOrReExtractAction(document)}
          >
            <SpriteExtractedIcon />
            {" "}
            {`${isReExtract(document) ? "Re Extract" : "View Extracted Data"}`}
            {" "}
          </Dropdown.Item>
        )}
        {canAddTag(document) && (
          <Dropdown.Item
            id="AddTagButton"
            onClick={() => hideContextMenu() & onAddTagging()}
          >
            <SpriteAddMoreTagsIcon />
            {isAddMoreTag ? "Add More Tags" : "Tag Document"}
          </Dropdown.Item>
        )}
        <Dropdown.Item onClick={() => hideContextMenu() & onRename(document)}>
          <SpriteRenameIcon />
          Rename
        </Dropdown.Item>
        {canClearTagging(document) && (
          <Dropdown.Item
            onClick={() => hideContextMenu() & onClearTagging()}
            disabled={disableClearTagging}
            className={disableClearTagging && "text-secondary"}
          >
            <SpriteClearTaggingIcon />
            Clear Tagging
          </Dropdown.Item>
        )}
        <Dropdown.Item onClick={() => hideContextMenu() & onDelete()}>
          <SpriteDeleteIcon />
          Delete
        </Dropdown.Item>
        <Dropdown.Item
          className="position-relative"
          onClick={() => hideContextMenu() & onNeedSupport()}
          disabled={hasTrialExhausted}
        >
          <SpriteNeedSupportIcon />
          Need Support
          <ProTag className="needSupport" />
        </Dropdown.Item>
      </Dropdown.Menu>
    </div>
  );
}

export default DocumentContextMenu;
