import * as Yup from 'yup';
import messages from '../../../../locales';
import {PROPERTY_TYPES} from '../../../constants';

// eslint-disable-next-line max-len
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const companyFormSchema = Yup.object().shape({
  name: Yup.string().trim().transform(value => (value === '' ? undefined : value)).required(messages.errors.validationError.companyName),
  assetTypes: Yup.array()
    .of(Yup.mixed().oneOf(Reflect.ownKeys(PROPERTY_TYPES)), messages.errors.validationError.assetTypes),
  address: Yup.object().shape({
    address: Yup.string(),
    country: Yup.string()
      .when('address', {
        is: (address) => address.length > 0,
        then: Yup.string().test('alphabets', 'Country must only contain alphabets', (value) => /^[A-Za-z]+$/.test(value)).required('Country is required with address.')
      }),
    zip: Yup.string()
      .when('address', {
        is: (address) => address.length > 0,
        then: Yup.string().min(5, 'Postal Code must be at least 5 characters').max(10, 'Postal Code can not be more than 10 characters').matches(
          /^[A-Z0-9]+$/i,
          {
            message: 'Only alphanumeric characters with min length 5 allowed.',
            excludeEmptyString: true
          },
        )
          .required('Postal Code is required with address.')
      })
  }),
  phone: Yup.string().transform(value => (!value ? null : value)).nullable().notRequired()
    .matches(phoneRegExp, 'Please provide a valid phone number.')
});

export default companyFormSchema;
