import React, { useState, useEffect, useCallback } from 'react';
import { IO } from '@grapecity/spread-excelio';
import { connect } from 'react-redux';
import { withServices } from 'reaf';
import { saveAs } from 'file-saver';
import { currentProjectReportSelector, currentProjectSelector, currentCompanySelector } from '../../../store/selectors';
import SpreadSheet from '../../core/SpreadSheet/SpreadSheet';
import ProjectReportWorkbookManager from './ProjectReportWorkbookManager';
import LoadingBackdropWithProgress from '../LoadingBackdrop/LoadingBackdropWithProgress';
import { appConfig } from '../../../config';
import ReportSpreadSheet from '../DocExtractionComponent/ReportSpreadSheet';

function ProjectReport({
  project, reports, store, apiClient, currentCompany, projectUuid, templateUuid, modelUuid
}) {
  const [manager, setManager] = useState(null);
  const createManager = useCallback((wbManager) => {
    setManager(new ProjectReportWorkbookManager(wbManager, {
      projectUuid,
      templateUuid,
      apiClient,
      modelUuid,
      companyUuid: currentCompany.modelUuid
    }));
  }, []);

  useEffect(() => {
    manager && manager.updateOptions({ store });
  }, [manager, store]);

  useEffect(() => {
    if (manager && reports) {
      const excelIO = new IO();
      manager.setReportData(reports, excelIO, store);
    }
  }, [manager, reports, store]);

  const downloadXLS = useCallback(blob => {
    const fileName = `${project.name}-report.xlsx`;
    saveAs(blob, fileName);
  }, []);

  return (
    <div className="vertical-section">
      {
        appConfig.canUseSpreadV2 ?
          <ReportSpreadSheet
            showToolbar 
            sheetCount={1}
            store = {store}
            reports = {reports}
            project = {project}
            projectUuid = {projectUuid}
            templateUuid = {templateUuid}
            apiClient = {apiClient}
            modelUuid = {modelUuid}
            companyUuid = {currentCompany?.modelUuid} 
          />
          : <SpreadSheet showToolbar isProjectReport sheetCount={1} onInitManager={createManager} onDownloadXLS={downloadXLS} />
      }
      <LoadingBackdropWithProgress />
    </div>
  );
}


const mapStateToProps = state => ({
  project: currentProjectSelector(state),
  reports: currentProjectReportSelector(state),
  currentCompany: currentCompanySelector(state)
});

export default connect(mapStateToProps)(withServices('store', 'apiClient')(ProjectReport));
