import {useState, useEffect} from 'react';
import {isNil} from 'lodash';

const checkAppVersion = (latestVersion, currentVersion) => latestVersion === currentVersion;

function CacheBuster({children}) {
  const [versionState, setVersionState] = useState({loading: true, isLatestVersion: false});

  const refreshCacheAndReload = () => {
    if (caches) {
      // Service worker cache should be cleared with caches.delete()
      caches.keys().then(async (names) => {
        await Promise.all(names.map(name => caches.delete(name)));
      });
    }
    // delete browser cache and hard reload
    window.location.reload();
  };

  const checkAppVersionConfiguration = async () => {
    const appBaseUrl = process.env.NODE_ENV === 'development' ? '' : `${window.location.origin}/${process.env.REACT_APP_APPBASEURL}`;

    const response = await fetch(`${appBaseUrl}/meta.json`);
    const data = await response.json();
    const latestVersion = data.version;
    const isAppVersionDoesNotExist = isNil(localStorage.getItem('appVersion'));

    if (isAppVersionDoesNotExist) {
      localStorage.setItem('appVersion', latestVersion);
      refreshCacheAndReload();
    }

    const currentVersion = parseInt(localStorage.getItem('appVersion'), 10);
    const isCurrentVersionEqualsToLatestVersion = checkAppVersion(latestVersion, currentVersion);

    if (isCurrentVersionEqualsToLatestVersion) {
      setVersionState({loading: false, isLatestVersion: true});
    } else {
      setVersionState({loading: false, isLatestVersion: false});
      localStorage.setItem('appVersion', latestVersion);
    }
  };

  useEffect(() => {
    checkAppVersionConfiguration();
  }, []);

  const {loading, isLatestVersion} = versionState;
  return children({loading, isLatestVersion, refreshCacheAndReload});
}

export default CacheBuster;
