import React from 'react';
import moment from 'moment';
import {DocumentFileTypes} from '../../../constants';
import style from './DocExtractionTaggingInfo.module.scss';

function DocExtractionTaggingInfo({docType, taggingInfo}) {
  if (Reflect.ownKeys(taggingInfo).length) {
    if (docType === DocumentFileTypes.RENT_ROLL.key) {
      const {asOnDate} = taggingInfo;
      return (
        <div className={style.taggingInfo}>
          <span>
As Of Date:&nbsp;
            <strong>{moment(asOnDate).format('MM/DD/YYYY')}</strong>
          </span>
        </div>
      );
    } else {
      const {periodFrom, periodTo} = taggingInfo;
      return (
        <div className={style.taggingInfo}>
          <span> From:&nbsp;
            <strong>{moment(periodFrom).format('MM/DD/YYYY')}</strong>
          </span>
          {' '}
          <small>&nbsp;</small>
          {' '}
          <span>To:&nbsp;
            <strong>{moment(periodTo).format('MM/DD/YYYY')}</strong>
          </span>
        </div>
      );
    }
  } else {
    return null;
  }
}

export default DocExtractionTaggingInfo;
