import React from 'react';
import {withServices} from 'reaf';
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import {displayDateFormat, customSortFunc} from '../../../utils/utils';
import TicketType from '../TicketType/TicketType';
import TicketStatus from '../TicketStatus/TicketStatus';
import DownloadTicketAttachment from '../DownloadTicketAttachment/DownloadTicketAttachment';
import {PAGINATION_DEFAULT} from '../../../constants';

const TicketListView = withServices('ticketService')(({
  ticketsList, onTicketSelect
}) => (
  <div className="table-pagination-position-static">
    <BootstrapTable
      data={ticketsList} striped
      hover condensed
      options={{onRowClick: onTicketSelect, sizePerPage: PAGINATION_DEFAULT.pageSize}}
      pagination>
      <TableHeaderColumn dataField="uuid" isKey dataAlign="right" width="50" dataFormat={uuid => uuid.substr(uuid.length - 4, uuid.length)}>#</TableHeaderColumn>
      <TableHeaderColumn dataField="description" dataSort>Ticket</TableHeaderColumn>
      <TableHeaderColumn dataField="type" width="100" dataSort dataFormat={(_, ticket) => <TicketType ticket={ticket} />}>Type</TableHeaderColumn>
      <TableHeaderColumn dataField="status" width="120" dataSort dataFormat={(_, ticket) => <TicketStatus ticket={ticket} />}> Status</TableHeaderColumn>
      <TableHeaderColumn dataField="Company" width="120" dataSort sortFunc={customSortFunc('Company.name')} dataFormat={company => company.name}>Company</TableHeaderColumn>
      <TableHeaderColumn dataField="User" width="120" dataSort sortFunc={customSortFunc('User.firstName')} dataFormat={user => (user ? `${user.firstName} ${user.lastName}` : 'User deleted')}>Raised By</TableHeaderColumn>
      <TableHeaderColumn dataField="updatedAt" width="120" dataSort dataFormat={displayDateFormat}>Updated On</TableHeaderColumn>
      <TableHeaderColumn
        width="60"
        dataAlign="center"
        dataField="S3Mapping" dataFormat={(S3Mapping, ticket) => <DownloadTicketAttachment ticket={ticket} />} />
    </BootstrapTable>
  </div>
));

export default TicketListView;
