import React, {
  useCallback, useState, useEffect
} from 'react';
import {Form, Formik} from 'formik';
import {connect} from 'react-redux';
import {withServices} from 'reaf';
import {
  Row, Col, Table, Button
} from 'react-bootstrap';
import {sumBy, isNil} from 'lodash';
import {setFloorPlan} from '../../../store/currentDocument';
import {currentDocumentFloorPlanSelector, currentDocumentFloorPlanBedSelector, currentDocumentFloorPlanBathSelector} from '../../../store/selectors';
import DocExtractionAffixTableWrapper from '../DocExtractionAffixTableWrapper/DocExtractionAffixTableWrapper';
import DocExtractionAffixTotalCard from '../DocExtractionAffixTotalCard/DocExtractionAffixTotalCard';
import FloorPlanDropDownOptions from './FloorPlanDropDownOptions';
import style from './DocExtractionFloorPlanConfig.module.scss';
import {displayNumberInUSFormat, getAverage, DisplayAmountFormat} from '../../../utils/utils';
import {rentRollAffixTabViewName, affixTotalColors} from '../../../constants';
import RentRollConfigHeader from '../RentRollConfigHeader/RentRollConfigHeader';
import DocExtractionWithoutSummary from '../DocExtractionSummary/DocExtractionWithoutSummary';
import DocExtractionFloorPlanChart from './DocExtractionFloorPlanChart';
import './DocExtractionFloorPlanConfig.scss';
import messages from '../../../../locales/en-US';
import FormSubmitButton from '../../core/Button/FormSubmitButton';

function DocExtractionFloorPlanConfig({
  floorPlanConfigData, floorPlanBedMapping, floorPlanBathMapping, store, onConfigSubmit, onSlidePaneClose
}) {
  const {TABLE_VIEW, CONFIG_VIEW} = rentRollAffixTabViewName;
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [activeViewName, setActiveViewName] = useState(TABLE_VIEW);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const {
    RENT_ROLL: {
      MULTIFAMILY: {
        AVG_SQFT,
        AVG_MARKET_RENT,
        AVG_MONTHLY_RENT,
        TOTAL_MONTHLY_RENT,
        TOTAL_UNITS,
        BEDS_BATHS
      }
    }
  } = affixTotalColors;

  const onSubmit = useCallback(async (...args) => {
    const [formValue] = args;
    setIsSubmitting(true);
    store.dispatch(setFloorPlan(formValue));
    await onConfigSubmit();
    setIsSubmitting(false);
  }, [onConfigSubmit, store]);

  const getUnitPercentage = useCallback((unit = 0) => {
    const unitCount = sumBy(Object.values(floorPlanConfigData), 'count');
    return Number(unit / unitCount * 100).toFixed(2);
  }, [floorPlanConfigData]);

  const getChartData = useCallback(() => {
    const chartConfig = [];
    Reflect.ownKeys(floorPlanConfigData).forEach(floorPlan => {
      const {
        count, marketRentTotal, monthlyRentTotal, squareFtTotal
      } = floorPlanConfigData[floorPlan];

      const avgMarketRent = getAverage(marketRentTotal, count);
      const avgMonthlyRent = getAverage(monthlyRentTotal, count);
      const avgsqft = getAverage(squareFtTotal, count);

      chartConfig.push({
        "name": floorPlan,
        'Market Rent': marketRentTotal,
        'Monthly Rent': monthlyRentTotal,
        'Vacancy Loss': marketRentTotal - monthlyRentTotal,
        'Avg. Market Rent': avgMarketRent,
        'Avg. Monthly Rent': avgMonthlyRent,
        'Avg. Loss': avgMarketRent - avgMonthlyRent,
        'Avg. SqFt': avgsqft
      });
    });
    return chartConfig;
  }, [floorPlanConfigData]);

  const activeViewClassName = useCallback((activeClassName) => (activeViewName === activeClassName ? '' : 'd-none'));

  const getBedBathTotals = useCallback((bedBathKey) => Reflect.ownKeys(floorPlanConfigData).reduce((total, item) => {
    total = total + floorPlanConfigData[item][bedBathKey] * floorPlanConfigData[item].count;
    return total;
  }, 0));

  const getCardTotals = useCallback((keyName, displayAmount = false) => {
    if (!isNil(displayNumberInUSFormat(sumBy(Object.values(floorPlanConfigData), keyName)))) {
      const total = sumBy(Object.values(floorPlanConfigData), keyName);
      return displayAmount ? <DisplayAmountFormat number={total} /> : total;
    } else {
      return 0;
    }
  });


  const getAverageTotals = useCallback((ObjKey, totalAmount, totalCount, displayAmount = true) => {
    if (floorPlanConfigData[ObjKey]) {
      const total = Math.round((floorPlanConfigData[ObjKey][totalAmount] / floorPlanConfigData[ObjKey][totalCount]));
      return displayAmount ? <DisplayAmountFormat number={total} /> : displayNumberInUSFormat(total);
    } else {
      return 0;
    }
  });

  const getTotalCardAvg = useCallback((keyName, unitCountKeyName, displayAmount = true) => {
    const totalAverage = Reflect.ownKeys(floorPlanConfigData).reduce((total, floorPlan) => {
      const average = (floorPlanConfigData[floorPlan][keyName] / floorPlanConfigData[floorPlan][unitCountKeyName]);
      total = total + (average * floorPlanConfigData[floorPlan][unitCountKeyName]);
      return total;
    }, 0);

    if (!isNil(totalAverage)) {
      const total = Math.round((totalAverage / sumBy(Object.values(floorPlanConfigData), unitCountKeyName)));
      return displayAmount ? <DisplayAmountFormat number={total} /> : displayNumberInUSFormat(total);
    } else {
      return 0;
    }
  });

  return (
    <>
      {
        Reflect.ownKeys(floorPlanConfigData).length > 0 ? (
          <>
            <RentRollConfigHeader
              title="Configure Floor Plan"
              customTitle="Configure FP"
              isCustomOptionVisible
              activeViewName={activeViewName}
              onTabViewChange={(activeView) => setActiveViewName(activeView)}
            />

            <Formik
              onSubmit={onSubmit}
              key="floorPlanKey"
              enableReinitialize
              initialValues={floorPlanConfigData}
              render={({
                setFieldValue,
                isValid
              }) => (
                <Form>
                  <DocExtractionAffixTableWrapper>
                    {
                      activeViewName === TABLE_VIEW || activeViewName === CONFIG_VIEW ? (
                        <>
                          <Row className={`configure-table-thead ${style.floorPlanHeader}`}>
                            <Col className={`col-md-auto ${style.fPlan} ${style.floorPlanWidth}`}>
                              FP name
                              <small className={activeViewClassName(TABLE_VIEW)}>Bed/Bath</small>
                            </Col>
                            <Col className={`col-md-auto ${style.avgsqft} ${activeViewClassName(TABLE_VIEW)}`}>
                              Avg. Sqft
                              <small>(Min - Max)</small>
                            </Col>
                            <Col className={`col-md-auto ${style.marketrent} ${activeViewClassName(TABLE_VIEW)}`}>
                              Avg. Market Rent
                              <small>(Min - Max)</small>
                            </Col>
                            <Col className={`col-md-auto ${style.monthlyrent} ${activeViewClassName(TABLE_VIEW)}`}>
                              Avg. Monthly Rent
                              <small>(Min - Max)</small>
                            </Col>
                            <Col className={`col-md-auto ${style.count} ${activeViewClassName(TABLE_VIEW)}`}>
                              #Units
                              <small>(Percentage)</small>
                            </Col>
                            <Col className={`col-md-auto ${style.beds} ${activeViewClassName(CONFIG_VIEW)}`}>#Beds</Col>
                            <Col className={`col-md-auto ${style.bath} ${activeViewClassName(CONFIG_VIEW)}`}>
                              #Baths
                            </Col>
                          </Row>
                          <Row className="position-relative">
                            <Col className={`${style.floorPlan} ${isMenuOpen ? `overflow-hidden` : 'overflow-auto'} ${activeViewName === CONFIG_VIEW ? 'configurefpWrapper' : ''}`}>
                              <Table>
                                <tbody>
                                  {
                                    Reflect.ownKeys(floorPlanConfigData).map((floorPlan) => (
                                      <tr key={floorPlan}>
                                        <td className={`col-md-auto ${style.fPlan}`}>
                                          <div className={style.fPlan}><span className="text-with-ellipsis w-100 d-inline-block">{floorPlan}</span></div>
                                          {
                                            <small
                                              aria-hidden
                                              role="menuitem"
                                              className={`${activeViewClassName(TABLE_VIEW)}`}
                                              onClick={() => setActiveViewName(CONFIG_VIEW)}>
                                              {/* eslint-disable-next-line max-len */}
                                              {`${floorPlanConfigData[floorPlan].BD}/${floorPlanConfigData[floorPlan].BA}`}
                                            </small>
                                          }
                                        </td>
                                        <td className={`${style.avgsqft} ${activeViewClassName(TABLE_VIEW)}`}>
                                          {getAverageTotals(floorPlan, 'squareFtTotal', 'count', false)}
                                          <small>
                                            {/* eslint-disable-next-line max-len */}
                                            {`(${displayNumberInUSFormat(floorPlanConfigData[floorPlan].squareFtMinValue)} - ${displayNumberInUSFormat(floorPlanConfigData[floorPlan].squareFtMaxValue)})`}
                                          </small>
                                        </td>
                                        <td className={`${style.marketrent} ${activeViewClassName(TABLE_VIEW)}`}>
                                          {getAverageTotals(floorPlan, 'marketRentTotal', 'count')}
                                          <small>
                                            {
                                              <>
                                                {'('}
                                                <DisplayAmountFormat
                                                  number={floorPlanConfigData[floorPlan].marketRentMinValue} />
                                                {' - '}
                                                <DisplayAmountFormat
                                                  number={floorPlanConfigData[floorPlan].marketRentMaxValue} />
                                                {')'}
                                              </>
                                            }
                                          </small>
                                        </td>
                                        <td className={`${style.monthlyrent} ${activeViewClassName(TABLE_VIEW)}`}>
                                          {getAverageTotals(floorPlan, 'monthlyRentTotal', 'count')}
                                          <small>
                                            {
                                              <>
                                                {'('}
                                                <DisplayAmountFormat
                                                  number={floorPlanConfigData[floorPlan].monthlyRentMinValue} />
                                                {' - '}
                                                <DisplayAmountFormat
                                                  number={floorPlanConfigData[floorPlan].monthlyRentMaxValue} />
                                                {')'}
                                              </>
                                            }
                                          </small>
                                        </td>
                                        <td className={`${style.count} ${activeViewClassName(TABLE_VIEW)}`}>
                                          {displayNumberInUSFormat(floorPlanConfigData[floorPlan].count)}
                                          <small>
                                            {
                                              `(${getUnitPercentage(floorPlanConfigData[floorPlan].count)} %)`
                                            }
                                          </small>
                                        </td>
                                        <FloorPlanDropDownOptions
                                          style={style}
                                          floorPlanConfigData={floorPlanConfigData}
                                          floorPlan={floorPlan}
                                          setCurrentFloorPlan={(dropdownValue) => {
                                            setFieldValue(floorPlan, dropdownValue)
                                          }}
                                          activeViewName={activeViewName}
                                          CONFIG_VIEW={CONFIG_VIEW}
                                          floorPlanBedMapping={floorPlanBedMapping}
                                          floorPlanBathMapping={floorPlanBathMapping}
                                          onMenuClose={() => setIsMenuOpen(false)}
                                          onMenuOpen={() => setIsMenuOpen(true)}
                                        />
                                      </tr>
                                    ))
                                  }
                                </tbody>
                              </Table>
                            </Col>
                          </Row>
                        </>
                      ) : (
                        <DocExtractionFloorPlanChart
                          style={style}
                          data={getChartData()}
                          xAxisKeyName="name"
                        />
                      )
                    }
                    {' '}
                    <DocExtractionAffixTotalCard
                      cards={[
                        {title: '#Beds/#Baths', color: BEDS_BATHS, content: `${getBedBathTotals('BD')} / ${getBedBathTotals('BA')}`},
                        {title: 'Avg. Sqft', content: getTotalCardAvg('squareFtTotal', 'count', false), color: AVG_SQFT},
                        {title: 'Avg. Market Rent', color: AVG_MARKET_RENT, content: getTotalCardAvg('marketRentTotal', 'count')}
                      ]}
                    />
                    <DocExtractionAffixTotalCard
                      cards={[
                        {title: 'Avg. Monthly Rent', color: AVG_MONTHLY_RENT, content: getTotalCardAvg('monthlyRentTotal', 'count')},
                        {title: 'Total Monthly Rent', color: TOTAL_MONTHLY_RENT, content: getCardTotals('monthlyRentTotal', true)},
                        {title: '#Units', color: TOTAL_UNITS, content: `${getCardTotals('count')} (100.00%)`}
                      ]}
                    />

                  </DocExtractionAffixTableWrapper>
                  <Row className={`configure-table-tfooter footerFixedBottom ${style.floorPlanFooter}`}>
                    <Button variant="outline-secondary btn-sm" onClick={onSlidePaneClose}>Cancel</Button>
                    <FormSubmitButton
                      isValid={isValid}
                      isSubmitting={isSubmitting}
                      id="floorPlanConfigSubmit"
                    />
                  </Row>
                </Form>
              )}
            />
          </>
        ) : <DocExtractionWithoutSummary message={messages.affixMessage.notFound} />
      }


    </>
  );
}

const mapStateToProps = state => ({
  floorPlanConfigData: currentDocumentFloorPlanSelector(state),
  floorPlanBedMapping: currentDocumentFloorPlanBedSelector(state),
  floorPlanBathMapping: currentDocumentFloorPlanBathSelector(state)
});

export default connect(mapStateToProps)(withServices('store')(DocExtractionFloorPlanConfig));
