import React, {useState, useCallback} from 'react';
import {Field, Form} from 'formik';
import {components} from 'reaf';
import ReCAPTCHA from 'react-google-recaptcha';
import {Form as BSForm, Card, Alert} from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import FieldErrorMessage from '../FieldErrorMessage/FieldErrorMessage';
import clikLogoImg from '../../../assets/images/clikai-logo.svg';
import Link from '../../core/Link/Link';
import style from './LoginFormComponent.module.scss';
import PasswordViewer from '../PasswordViewer/PasswordViewer';
import {setCaptchaToken} from '../../../store/auth';
import {captcha} from '../../../config/index';
import {SpriteEmailIcon} from '../../core/SpriteIcon/SpriteIcon';

function LoginFormComponent({
  status, isSubmitting, recaptchaRef, store
}) {
  const [isCaptchaVerified, setCaptchaVerified] = useState('');

  const setCaptcha = useCallback((t) => {
    setCaptchaVerified(t);
    store.dispatch(setCaptchaToken(t));
  }, []);

  return (
    <Card className="form-card">
      <Card.Body className={`form-card-body ${style.formCardBody}`}>
        <div className="loginFormLogo">
          <img src={clikLogoImg} alt="Clik Logo" />
        </div>
        {
          status && status.error &&
          (
            <Alert variant="danger">
              <p>{status.error}</p>
            </Alert>
          )
        }

        <Form autoComplete="off">
          <BSForm.Group controlId="email" className="position-relative">
            <BSForm.Label>Email Address</BSForm.Label>
            <div className="loginFormIcon">
              <SpriteEmailIcon />
            </div>
            <Field
              id="email"
              className={`form-control ${style.formControl}`}
              type="email"
              name="email"
              autoComplete="off"
              placeholder="Enter your email address"
            />
            <FieldErrorMessage name="email" />
          </BSForm.Group>

          <BSForm.Group controlId="password" className="passwordGroupRow">
            <BSForm.Label>Password</BSForm.Label>
            <PasswordViewer autoComplete="new-password" id="password" name="password" placeholder="Enter your password" />
            <FieldErrorMessage name="password" />
          </BSForm.Group>

          <BSForm.Group>
            <ReCAPTCHA
              ref={recaptchaRef}
              sitekey={captcha.siteKey}
              onChange={(t) => setCaptcha(t)}
            />
          </BSForm.Group>

          <Button
            className={`btn btn-primary btn-block ${style.btnPrimary}`}
            type="submit"
            disabled={isSubmitting || !isCaptchaVerified}
          >
            Login
          </Button>
        </Form>

        <small>
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <Link className="text-secondary text-decoration-none mt-3 d-inline-block" route="forgotPasswordPage">
            Forgot Password?
          </Link>
        </small>
      </Card.Body>
    </Card>
  );
}


export default components.withServices('store')(LoginFormComponent);
