import {sortBy} from 'lodash';
import {setTemplates, setTemplateModels} from '../store/company';
import {updateCompany} from '../store/companyList';

import {setAuth} from '../store/auth';
import {forceUTCTimeZone, isBlankString} from '../utils/utils';

class CompanyService {
  constructor(apiClient, store, companyListService, authService) {
    this.authService = authService;
    this.apiClient = apiClient;
    this.store = store;
    this.companyListService = companyListService;
  }

  setToastrService(toastrService) {
    this.toastrService = toastrService;
  }

  async loadCompanyTemplates() {
    const result = await this.apiClient.get(
      '/companies/templates',
    );
    const {templates} = result.response;
    const sortedTemplates = sortBy(templates, [template => template.templateName.toLowerCase()]);
    this.store.dispatch(setTemplates(sortedTemplates));
    return templates;
  }

  async upsertCompany(company, values) {
    let responseCompany;
    const notificationText = (isBlankString(values.company.notificationText)) ? null : values.company.notificationText;
    const numberOfLicenses = typeof (values.company.numberOfLicenses) === 'string' ?
      (isBlankString(values.company.numberOfLicenses)) ? null : values.company.numberOfLicenses : values.company.numberOfLicenses;
    if (company.uuid) {
      let paymentDueDate = values.paymentDueDate !== '' ? forceUTCTimeZone(values.paymentDueDate) : company.paymentDueDate;
      if (values.previousDueDate !== '' && values.paymentStatus === 'Completed') {
        paymentDueDate = null;
      }
      const updatedCompany = {
        ...values.company, paymentDueDate, notificationText, numberOfLicenses
      };
      responseCompany = await this.updateCompany(company.uuid, {company: updatedCompany});
    } else {
      responseCompany = await this.createCompany({
        company: {
          ...values.company, paymentDueDate: null, notificationText, numberOfLicenses
        },
        admin: values.admin
      });
    }
    return responseCompany;
  }

  async setCompanyAccess(company, accessEnabled) {
    const result = await this.apiClient.put(
      `/companies/${company.uuid}`,
      {
        company: {
          accessEnabled
        }
      },
    );
    return result.response;
  }

  async setCompanyAcl(company, isAclEnabled) {
    const result = await this.apiClient.put(
      `/companies/${company.uuid}`,
      {
        company: {
          isAclEnabled
        }
      },
    );
    return result.response;
  }

  async updateCompanySSO(company, {isSSOEnabled, slug}) {
    const result = await this.apiClient.put(
      `/companies/${company.uuid}`,
      {
        company: {
          isSSOEnabled,
          slug
        }
      },
    );
    const companyDetails = result.response;
    return companyDetails;
  }

  async createCompany(values) {
    const result = await this.apiClient.post(
      '/companies',
      values,
    );
    return result.response;
  }

  async updateCompany(uuid, values) {
    const result = await this.apiClient.put(
      `/companies/${uuid}`,
      values,
    );

    return result.response;
  }

  async deleteCompany({company}) {
    const result = await this.apiClient.delete(
      `/admin/companies/${company.company.uuid}`,
    );
    return result.response;
  }

  async loadCompanyTemplateModels(companyUuid) {
    const result = await this.apiClient.get(
      `/companies/${companyUuid}/templatesWithModel`
    );
    return result.response;
  }

  registerSideEffects(sideEffect) {
    sideEffect.after(setAuth, async ([user, token, role, company], {dispatch}) => {
      if (!user.hasAgreedTerms) {
        return false;
      }

      if (!(this.authService.isSuperAdmin() || this.authService.isAccountsExecutive()) && company) {
        const templateModels = await this.loadCompanyTemplateModels(company.uuid);
        const sortedTemplateModels = sortBy(templateModels, [template => template.templateName.toLowerCase()]);
        dispatch(setTemplateModels(sortedTemplateModels));
      }
    });
  }

  async createSupport(values, selectedAttachment) {
    const endPoint = `/tickets`;
    const formData = new FormData();
    Reflect.ownKeys(values).forEach(key => {
      formData.append(key, values[key]);
    });
    formData.append('file', selectedAttachment);

    const result = await this.apiClient.post(
      endPoint,
      formData
    );

    this.toastrService.success(result.message);
    return result;
  }
}

export default CompanyService;
