import React from 'react';
import {connect} from 'react-redux';
import {withServices} from 'reaf';
import {Container, Row, Col} from 'react-bootstrap';
import {
  currentCompanySelector,
  companiesDetailsSelector
} from '../../../store/selectors';
import SuperAdminDashboard from '../SuperAdminDashboard/SuperAdminDashboard';
import UsersList from '../../app/User/UsersList';
import Breadcrumbs from '../../app/Breadcrumbs/Breadcrumbs';

function UserListPageHeader({companyUUID, companiesDetails}) {
  return (
    <Container fluid className="p-2 light border-bottom-light-blue sa-breadcrumb">
      <Row className="align-items-center">
        <Col>
          <Breadcrumbs
            rootRoute="superAdminHomePage" items={[
              {route: 'superAdminPage', content: 'Companies'},
              {route: 'superAdminUserManagement', content: companiesDetails[companyUUID].name, params: {companyUUID: companiesDetails[companyUUID].uuid}},
              {route: 'superAdminUserManagement', content: 'Users', params: {companyUUID: companiesDetails[companyUUID].uuid}}
            ]} />
        </Col>
      </Row>
    </Container>
  );
}

function UsersListPage({companyUUID, companiesDetails}) {
  return (
    <SuperAdminDashboard>
      <UserListPageHeader companyUUID={companyUUID} companiesDetails={companiesDetails} />
      <UsersList />
    </SuperAdminDashboard>
  );
}
const mapStateToProps = state => ({
  companyUUID: currentCompanySelector(state),
  companiesDetails: companiesDetailsSelector(state)
});
export default connect(mapStateToProps)(withServices('userService')(UsersListPage));
