import React from 'react';
import {get} from 'lodash';

function filter(searchText, filterHandler, nestedSearchKey) {
  filterHandler({
    callback: searchValue => (searchText ?
      searchValue &&
        get(searchValue, nestedSearchKey, '').toLowerCase().indexOf(searchText.toLowerCase()) !== -1 :
      true)
  });
}

class CustomFilter extends React.Component {
  render() {
    return (
      <input type="text" className="filter text-filter form-control" placeholder="Filter" onChange={e => filter(e.target.value, this.props.filterHandler, this.props.nestedSearchKey)} />
    );
  }
}

export class CustomSelectFilter extends React.Component {
  filterOptions = Reflect.ownKeys(this.props.options).map(key => (<option value={key} key={key}>{this.props.options[key].label}</option>));

  render() {
    return (
      <select className="filter select-filter form-control placeholder-selected" onChange={e => filter(e.target.value, this.props.filterHandler, this.props.nestedSearchKey)}>
        <option value="">Choose...</option>
        {
          this.filterOptions
        }
      </select>
    );
  }
}

export default CustomFilter;
