import React, {useCallback, useMemo, useState} from 'react';
import {connect} from 'react-redux';
import {matchSorter} from 'match-sorter';
import TicketsKanbanBoard from '../TicketsKanbanBoard/TicketsKanbanBoard';
import useBool from '../../../hooks/useBool';
import {getTicketsAsListSelector} from '../../../store/selectors';
import TicketListHeader from '../TicketListHeader/TicketListHeader';
import TicketListView from './TicketListView';
import TicketPopups from '../TicketPopups/TicketPopups';

const Tickets = ({ticketsList}) => {
  const [isListView, , , toggleListView] = useBool(true);
  const [showTicketPopoup, setShowTicketPopup, setHideTicketPopup] = useBool(false);
  const [selectedTicket, setSelectedTicket] = useState();
  const onTicketSelect = useCallback((ticket) => {
    setSelectedTicket(ticket);
    setShowTicketPopup();
  }, []);

  const [filteredTicketList, setFilteredTicketList] = useState(ticketsList);

  const filterTickets = useCallback((searchText) => {

    searchText
      ? setFilteredTicketList(matchSorter(ticketsList, searchText, {keys: ['description', 'Company.name', 'User.firstName', 'User.lastName']}))
      : setFilteredTicketList(ticketsList);

  }, [ticketsList]);

  const View = isListView ? TicketListView : TicketsKanbanBoard;
  return (
    <>
      <TicketListHeader toggleListView={toggleListView} filterTickets={filterTickets} />
      <View ticketsList={filteredTicketList} onTicketSelect={onTicketSelect} />
      <TicketPopups
        showTicketPopoup={showTicketPopoup}
        ticket={selectedTicket}
        onTicketUpdate={(ticket) => setSelectedTicket(ticket)} setHideTicketPopup={setHideTicketPopup} />
    </>
  );
};

const mapStateToProps = (state) => ({
  ticketsList: getTicketsAsListSelector(state)
});
export default connect(mapStateToProps)(Tickets);
