import React, {useCallback} from 'react';
import {connect} from 'react-redux';
import ItemWithContextMenu from '../ItemTile/ItemWithContextMenu';
import folderFile from '../../../assets/images/folder.svg';
import ProjectContextMenu from '../ProjectContextMenu/ProjectContextMenu';
import LargeTile from '../ItemTile/LargeTile';
import {PROPERTY_TYPES} from '../../../constants';
import {userAllowedTemplatesSelector} from '../../../store/selectors';

function formatDate(str) {
  const date = new Date(str);
  return `${date.getMonth() + 1}/${date.getDate()}/${date.getFullYear()}`;
}

function ProjectTile({
  project, documentsSummary = {}, templates, selected, onNeedSupport, onEdit, onDelete, onClearTagging, onOpen, onClick, ...restProps
}) {
  const {
    count = 0, tagged = 0, converted = 0
  } = documentsSummary;
  const tlBadgeText = PROPERTY_TYPES[project.assetType] ? PROPERTY_TYPES[project.assetType].badgeText : '';
  const disableClearTagging = useCallback((project) => templates[project.templateUuid].keysToMask.length > 0, [templates]);

  return (
    <ItemWithContextMenu
      TileComponent={() => (
        <LargeTile
          name={project.name}
          icon={folderFile}
          onDoubleClick={onOpen}
          onClick={onClick}
          selected={selected}
          tlBadgeText={tlBadgeText}
          {...restProps}
        />
      )}
      ContextMenuComponent={({hideContextMenu}) => (
        <ProjectContextMenu
          project={project}
          hideContextMenu={hideContextMenu}
          onEdit={onEdit}
          onDelete={onDelete}
          onClearTagging={onClearTagging}
          onOpen={onOpen}
          onNeedSupport={onNeedSupport}
          documentsSummary={documentsSummary}
          disableClearTagging={disableClearTagging(project)}
        />
      )}
    />
  );
}

const mapStateToProps = (state) => ({
  templates: userAllowedTemplatesSelector(state)
});

export default connect(mapStateToProps)(ProjectTile);
