import React from 'react';

import {Form} from 'formik';
import FormikSelectField from "../../core/FormikSelectField/FormikSelectField";
import FormikTextArea from "../../core/FormikTextArea/FormikTextArea";
import FileUploader from '../FileUploader/FileUploader';
import {SUPPORT_TYPES} from '../../../constants';

export default function NeedSupportFormComponent({
  status,
  isSubmitting,
  bindSubmission,
  submitForm,
  onAttachmentChange,
  onAttachmentRemove,
  selectedAttachment
}) {
  bindSubmission(submitForm, isSubmitting);
  return (
    <div>
      {
        status && status.error &&
        (
          <div className="alert alert-danger">
            <p>{status.error}</p>
          </div>
        )
      }

      <Form>
        <FormikSelectField name="type" label="Type">
          {
            Reflect.ownKeys(SUPPORT_TYPES).map(issueType => (
              <option key={issueType} value={issueType}>{SUPPORT_TYPES[issueType].label}</option>
            ))
          }
        </FormikSelectField>
        <FormikTextArea name="description" label="Description" />
        <FileUploader
          onFileChange={onAttachmentChange}
          onRemoveFile={onAttachmentRemove}
          title="Attachment"
          selectedFile={selectedAttachment}
          allowedFileSize="10" />
      </Form>
    </div>
  );
}
