import React, {useCallback, useRef} from 'react';
import {Formik} from 'formik';
import {components} from 'reaf';
import * as Yup from 'yup';
import messages from '../../../../locales';
import LoginFormComponent from './LoginFormComponent';
import {authSelector} from '../../../store/selectors';

const LoginFormSchema = Yup.object().shape({
  email: Yup.string()
    .email(messages.errors.auth.invalidEmail)
    .required(messages.errors.auth.invalidEmail),
  password: Yup.string()
    .required(messages.errors.auth.requiredPassword)
});

function LoginForm({authService, store, redirectOnSuccess = true}) {
  const recaptchaRef = useRef(null);

  const onSubmit = useCallback(async (values, actions) => {
    values.captchaToken = authSelector(store.getState()).captchaToken;
    actions.setStatus({});
    try {
      await authService.login(values, redirectOnSuccess);
    } catch (e) {
      console.log(e);
      actions.setStatus({error: e.message});
      recaptchaRef.current.reset();
    }

    actions.setSubmitting(false);
  }, []);

  return (
    <Formik
      initialValues={{email: '', password: ''}}
      validationSchema={LoginFormSchema}
      onSubmit={onSubmit}
      component={
        formikProps => (
          <LoginFormComponent
            {...formikProps}
            recaptchaRef={recaptchaRef}
          />
        )}
    />
  );
}

const LoginFormSimple = components.withServices('store', 'authService')(LoginForm);

export const LoginFormMemo = React.memo(LoginFormSimple);

export default LoginFormSimple;
