import React, {useCallback, useMemo} from 'react';
import {withServices} from 'reaf';
import DocumentsService from '../../../services/DocumentsService';
import {FILE_TYPES} from '../../../constants';
import PdfViewer from '../PdfViewer/PdfViewer';
import XLViewer from '../XLViewer/XLViewer';


function DocumentViewer({
  project, document, setPdfPageCount, setXLSheetNames, documentsService, small = false, viewerHeight, sheetHeight = {}, activeSheetName = null
}) {
  const cache = useMemo(() => ({}), []);

  if (!cache[document.uuid]) {
    cache[document.uuid] = documentsService.getDocumentFileUrlObject(project, document);
  }

  const fileObj = cache[document.uuid];


  const onDataLoad = useCallback(({sheetNames}) => {
    if (setXLSheetNames) {
      setXLSheetNames(sheetNames);
    }
  }, [setXLSheetNames]);

  const onPdfPageCountSet = useCallback((pageCount) => {
    if (setPdfPageCount) {
      setPdfPageCount(pageCount);
    }
  }, [setPdfPageCount]);


  const documentType = DocumentsService.getDocumentType(document);
  if (documentType === FILE_TYPES.PDF.key) {
    return <PdfViewer fileObj={fileObj} onSetPageCount={onPdfPageCountSet} small={small} viewerHeight={viewerHeight} />;
  } if (documentType === FILE_TYPES.XL.key) {
    return <XLViewer document={document} project={project} onDataLoad={onDataLoad} activeSheetName={activeSheetName} sheetHeight={sheetHeight} />;
  }
}

export default withServices('documentsService')(DocumentViewer);
