import React, {
  useEffect, useState, useRef
} from 'react';
import {withServices} from 'reaf';
import SpreadSheet from '../../core/SpreadSheet/SpreadSheet';
import loadingIcon from '../../../assets/images/Gear-4s-60px.svg';
import {SpreadSheets} from '../../core/Spreadjs/SpreadSheets';
import {appConfig} from '../../../config';

function XLViewer({
  project, document, documentsService, onDataLoad, sheetHeight = {}, activeSheetName = null
}) {
  const [data, setData] = useState(null);
  const [showFallback, setShowFallback] = useState(false);
  const spreadSheetRef = useRef();

  useEffect(() => {
    documentsService.getJsonFromXLDocumentFile(project, document)
      .then((jsonData) => {
        setShowFallback(false);
        setData(jsonData);
      })
      .catch(() => {
        setShowFallback(true);
      });
  }, [project, document, documentsService]);

  useEffect(() => {
    if (data && spreadSheetRef.current) {
      const sheet = spreadSheetRef.current;
      sheet.workbook.fromJSON(data);
      sheet.setReadonly(true);
      const sheetNames = sheet.getSheetNames();

      onDataLoad && onDataLoad({
        sheetNames
      });
    }
  }, [data, spreadSheetRef.current]);

  return (
    <div className="vertical-section">
      {
        // eslint-disable-next-line no-nested-ternary
        data ?
          (appConfig.canUseSpreadV2 ?
            <SpreadSheets ref={spreadSheetRef} showToolbar={false} readonly onReady={() => { }} sheetHeight={sheetHeight} /> :
            <SpreadSheet readonly data={data} onDataLoad={onDataLoad} activeSheetName={activeSheetName} hasAutoWidth sheetHeight={sheetHeight} />
          ) :
          !showFallback ?
            (
              <div className="d-flex justify-content-center mt-5">
                <img src={loadingIcon} height={20} alt="Loading Icon" />
                <span className="h6 ml-2 mt-auto mb-auto">Loading Document</span>
              </div>
            ) :
            (
              <div className="d-flex justify-content-center mt-5">
                <span className="h6 ml-2 mt-auto mb-auto">App viewers do not support XLS file format. Please save the file to XLSX format and upload it again.</span>
              </div>
            )
      }
    </div>
  );
}

export default withServices('documentsService')(XLViewer);
