import React, {useState} from 'react';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

function ConfirmationModal({
  title, message, onConfirm, onCancel, show, isConfirm = true
}) {
  const buttonText = isConfirm ? 'Confirm' : 'OK';
  const [doubleClick, setDoubleClick] = useState(false);

  return (
    <Modal show={show} onHide={onCancel} backdrop="static">
      <Modal.Header closeButton>
        <Modal.Title title={title}>
          {title}
        </Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <p>{message}</p>
      </Modal.Body>

      <Modal.Footer>
        {
          isConfirm &&
          <Button variant="outline-danger" size="sm" type="button" onClick={onCancel}>Cancel</Button>
        }
        <Button
          variant="primary" type="button" disabled={doubleClick} onClick={() => {
            onConfirm();
            setDoubleClick(true);
          }}>
          {buttonText}
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ConfirmationModal;
