import React, {
  useState, useEffect, useCallback, useMemo
} from 'react';
import {Form} from 'react-bootstrap';
import ReactSelect from 'react-select';
import {withServices} from 'reaf';
import {connect} from 'react-redux';
import {
  companyTemplatesAsListSelector
} from '../../../store/selectors';

const getTemplateDropdownMap = (companyTemplates) => companyTemplates.map(({uuid: value, templateName: label}) => ({value, label}));

function UserTemplateAccessForm({
  onSubmitSuccess, bindSubmission, user, companyUUID, userService, toastrService, companyTemplates
}) {
  const [selectAll, setSelectAll] = useState(false);
  const [initialTemplateAccess, setInitialTemplateAccess] = useState([]);
  const [selectedTemplates, setSelectedTemplates] = useState(initialTemplateAccess);
  const companyTemplatesMap = useMemo(() => getTemplateDropdownMap(companyTemplates),
    [companyTemplates]);

  useEffect(() => {
    const fetchTemplates = async () => {
      const fetchedTemplates = await userService.getTemplateAccess(user.user.uuid);
      let initialTemplateAccess = [];
      if (fetchedTemplates.length) {
        initialTemplateAccess = getTemplateDropdownMap(fetchedTemplates);
      } else {
        initialTemplateAccess = companyTemplatesMap;
        setSelectAll(true);
      }
      setInitialTemplateAccess(initialTemplateAccess);
      setSelectedTemplates(initialTemplateAccess);
    };
    fetchTemplates();
  }, [user, companyTemplatesMap]);

  const onSubmit = useCallback(async () => {
    try {
      const response = await userService.updateTemplateAccess({templateUuids: selectAll ? [] : selectedTemplates.map(({value}) => value)}, user.user.uuid);
      toastrService.success(response.message);
      onSubmitSuccess();
    } catch (error) {
      console.error(error);
    }
  }, [selectedTemplates, user.user]);

  useEffect(() => {
    bindSubmission(onSubmit, false);
  }, [onSubmit]);

  return (
    <>
      <Form.Group controlId="formBasicCheckbox">
        <Form.Check
          type="checkbox" checked={selectAll} onChange={e => {
            setSelectAll(e.target.checked);
            setSelectedTemplates(companyTemplatesMap);
          }} label="Allow all templates." />
      </Form.Group>
      <ReactSelect
        isMulti
        isDisabled={selectAll}
        value={selectedTemplates}
        onChange={selectedTempates => {
          setSelectedTemplates(selectedTempates);
        }}
        options={companyTemplatesMap} />
    </>
  );
}
const mapStateToProps = state => ({
  companyTemplates: companyTemplatesAsListSelector(state)
});
export default connect(mapStateToProps)(withServices('userService', 'toastrService')(UserTemplateAccessForm));
