import axios from 'axios';
import qs from 'qs';
import messages from '../../locales';
import {apiConfig} from '../config';
import APIError from '../utils/APIError';
import {setProductTourState, markFirstTourDone} from '../store/productTour';
import {productTourSelector} from '../store/selectors';

axios.interceptors.request.use(config => {
  config.paramsSerializer = params => qs.stringify(params, {
    arrayFormat: "brackets",
    encode: false
  });
  return config;
});
class ApiClient {
  setAuthService(authService) {
    this.authService = authService;
  }

  getHeaders(configHeaders) {
    const headers = {
      'Content-Type': 'application/json'
    };

    if (this.authService.isLoggedIn()) {
      headers.Authorization = `Bearer ${this.authService.authState().token}`;
    }

    return {
      ...headers,
      ...configHeaders
    };
  }

  async request(config) {
    try {
      const headers = this.getHeaders(config.headers || {});
      const result = await axios({
        ...config,
        url: `${apiConfig.baseUrl}${config.url}`,
        headers,
        mode: 'cors'
      });

      return result.data;
    } catch (error) {
      if (error.response && error.response.status === 500) {
        throw new Error(messages.errors.serverError);
      } else if (error.response && error.response.status === 401) {
        if (this.authService.isLoggedIn()) {
          this.authService.logout();
        }
      }

      throw new APIError(error.response.status, error.response.data.message, error.response.data.error);
    }
  }

  async get(url, config = {}) {
    return this.request({
      ...config,
      url,
      method: 'get'
    });
  }

  async post(url, data, config) {
    return this.request({
      ...config,
      url,
      data,
      method: 'post'
    });
  }

  async delete(url, data, config) {
    return this.request({
      ...config,
      url,
      data,
      method: 'delete'
    });
  }

  async put(url, data, config) {
    return this.request({
      ...config,
      url,
      data,
      method: 'put'
    });
  }

  registerSideEffects(sideEffect) {
    sideEffect.boot(({dispatch}) => {
      const productTourInfo = JSON.parse(localStorage.getItem('autouw_productTour') || '{}');
      dispatch(setProductTourState(productTourInfo));
    });

    sideEffect.after(markFirstTourDone, (_, {getState}) => {
      const productTourInfo = productTourSelector(getState());
      localStorage.setItem('autouw_productTour', JSON.stringify(productTourInfo));
    });
  }
}

export default ApiClient;
