import React, {useMemo} from 'react';
import Tour from 'reactour';
import Button from 'react-bootstrap/Button';

function ProjectPageTour({show, onClose, onAddFileTutorial}) {
  const steps = useMemo(() => projectPageSteps(onAddFileTutorial, onClose), [onClose, onAddFileTutorial]);

  return (
    <Tour
      steps={steps}
      isOpen={show}
      onRequestClose={onClose}
      rounded={5}
      accentColor="#2196f2"
      startAt={0}
      disableInteraction
    />
  );
}

