import {showToastr} from "../store/notificationToastr";

class ToastrService {
  constructor(store) {
    this.store = store;
  }

  success(msg, position) {
    this.store.dispatch(showToastr('success', msg, position));
  }

  error(msg, position) {
    this.store.dispatch(showToastr('danger', msg, position));
  }

  info(msg, position) {
    this.store.dispatch(showToastr('info', msg, position));
  }
}

export default ToastrService;
