import React, {useCallback} from 'react';
import Dropdown from 'react-bootstrap/Dropdown';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCaretDown} from '@fortawesome/free-solid-svg-icons/faCaretDown';
import {connect} from 'react-redux';
import {withServices} from 'reaf';
import {appConfig} from '../../../config';
import {authSelector, userRoleSelector} from '../../../store/selectors';
import avatarFile from '../../../assets/images/avatar.svg';
import clikLogoFile from '../../../assets/images/logo-symbol.png';
import whatsNewIcon from '../../../assets/images/notification.svg';
import Link from '../../core/Link/Link';
import style from './UserHeader.module.scss';
import {USER_ROLES} from '../../../constants';
import {SpriteSettingsIcon, SpriteLogoutIcon} from '../../core/SpriteIcon/SpriteIcon';
import tutorialIcon from '../../../assets/images/tutorial.svg';

class UDWrapper extends React.Component {
  render() {
    return (
      <div onClick={this.props.onClick} className="clearfix cursor-pointer">
        {this.props.children}
      </div>
    );
  }
}

const whatsNewIconEl = () => <img src={whatsNewIcon} className={style.navbarHeaderIcons} alt={'what\'s new'} />;
const tutorialIconEl = () => <img src={tutorialIcon} alt="tutorial" />;

const ReleaseInfo = withServices('router')(({router}) => {
  const setRedirection = () => {
    const section = appConfig.isFreebieApp ? 'tips' : 'release';
    router.setRoute('accountSettingsPage', {section: section});
  };

  return (
    <i className={`nav-link py-0 ${style.whatsNew} ${style.tutorial}`} onClick={() => setRedirection()}>
      {!appConfig.isFreebieApp && <span className={style.bellText}> What's new</span>}
      {appConfig.isFreebieApp ? tutorialIconEl() : whatsNewIconEl()}
    </i>
  )
});

function UserHeader({
  userName, userRole, companyName, authService, router
}) {
  const logout = useCallback(() => authService.logout(), []);
  const logoRoute = authService.resolveRoute();

  return (
    <header className={`navbar navbar-expand navbar-dark justify-content-between bd-navbar bg-dark text-white ${style.header}`}>
      <Link route={logoRoute} className={`text-white ${style.navbarBrand}`}>
        <img src={clikLogoFile} alt="Clik Logo" />
        <span className="ml-2"><small><strong>{companyName}</strong></small></span>
      </Link>
      <ul className={`navbar-nav align-items-center ml-md-auto ${style.accountHeader}`}>
        {/* <li className="nav-item">
          <ReleaseInfo />
        </li> */}
        <li className="nav-item pl-2">
          <Dropdown className={style.avatar}>
            <Dropdown.Toggle as={UDWrapper}>
              <div className={`clearfix ${style.userBdrHover}`}>
                <span className={style.avatarIcon}>
                  <img src={avatarFile} alt="User avatar" />
                </span>
                <span>
                  <FontAwesomeIcon icon={faCaretDown} />
                </span>
              </div>
            </Dropdown.Toggle>
            <Dropdown.Menu className={style.dropdownMenuRight}>
              <div className={`mt-n1 ${style.clikUserName}`}>
                <span className={style.userNameText}>{userName}</span>
                <small className="text-secondary">{userRole ? USER_ROLES[userRole].label : ''}</small>
              </div>
              <div className="dropdown-divider" role="separator" />
              <Dropdown.Item onClick={() => router.setRoute('accountSettingsPage', {section: 'profile'})}>
                <SpriteSettingsIcon />
                Account Settings
              </Dropdown.Item>
              <Dropdown.Item onClick={logout}>
                <SpriteLogoutIcon />
                Logout
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </li>
      </ul>
    </header>
  );
}

const mapStateToProps = (state) => {
  const auth = authSelector(state);

  return auth.user ? {
    userName: [auth.user.firstName, auth.user.lastName].join(' '),
    userRole: userRoleSelector(state),
    companyName: auth.company.name
  } : {};
};

export default connect(mapStateToProps)(withServices('authService', 'router')(UserHeader));
