import React from 'react';
import {Formik} from 'formik';
import * as Yup from 'yup';
import {withServices} from 'reaf';
import UserFormComponent from './UserFormComponent';
import {USER_ROLES, PASSWORD_SCHEMA} from '../../../constants';

const UserFormSchema = Yup.object().shape({
  user: Yup.object().shape({
    firstName: Yup.string().trim().transform(value => (value === '' ? undefined : value)).required('Please provide a valid first name.'),
    lastName: Yup.string().trim().transform(value => (value === '' ? undefined : value)).required('Please provide a valid last name.'),
    email: Yup.string().email('Please provide a valid email.').required('Please provide a valid email'),
    password: PASSWORD_SCHEMA
  }),
  role: Yup.mixed().oneOf([USER_ROLES.COMPANY_USER.key, USER_ROLES.SUPPORT_USER.key], 'Please select a valid user role.')
});

function UserForm({
  onSubmitSuccess, bindSubmission, user, companyUUID, userService
}) {
  async function onSubmit(values, actions) {
    actions.setStatus({});
    try {
      await userService.createCompanyUser(values, companyUUID);
      onSubmitSuccess();
    } catch (error) {
      actions.setStatus({error: error.message});
    }
    actions.setSubmitting(false);
  }

  return (
    <Formik
      initialValues={user}
      validationSchema={UserFormSchema}
      onSubmit={onSubmit}
      component={formikProps => <UserFormComponent {...formikProps} bindSubmission={bindSubmission} />
      }
    />
  );
}

export default withServices('userService')(UserForm);
