import React from 'react';
import {Field, Form} from 'formik';
import {Alert, Card, Form as BSForm} from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import FieldErrorMessage from '../FieldErrorMessage/FieldErrorMessage';
import PasswordViewer from '../PasswordViewer/PasswordViewer';
import style from './ChangePasswordFormComponent.module.scss';
import keyIcon from '../../../assets/images/key.svg'
import {PasswordStrengthInfo} from '../PasswordStrengthInfo/PasswordStrengthInfo';

function ChangePasswordFormComponent({status}) {
  return (
    <div className={`${style.changePasswordOuter} passwordGroupRow`}>
      <div className="row">
        <div className={`col-12 d-flex border-bottom ${style.sectionHeader}`}>
          <h3>Change Password</h3>
        </div>
      </div>
      <Card className={style.formWrapper}>
      {
        status && status.error &&
        (
          <Alert variant="danger" className="mx-5">
            <span>{status.error}</span>
          </Alert>
        )
      }

      {
        status && status.success &&
        (
          <Alert variant="success" className="mx-5">
            <span>{status.success}</span>
          </Alert>
        )
      }

      <Form>
        <BSForm.Group controlId="oldPassword" className={`row ${style.formControlGroup}`}>
          <BSForm.Label>Current Password</BSForm.Label>
          <div className={style.fromControl}>
            <div className="loginFormIcon passwordIcon">
              <img src={keyIcon} alt="keyIcon"/>
            </div>
            <PasswordViewer showIcon={false} id="oldPassword" name="oldPassword" placeholder="Enter your current password" />
            <FieldErrorMessage name="oldPassword" />
          </div>
        </BSForm.Group>

        <BSForm.Group controlId="newPassword" className={`row ${style.formControlGroup}`}>
          <BSForm.Label>New Password
            <PasswordStrengthInfo />
          </BSForm.Label>
          <div className={style.fromControl}>
            <div className="loginFormIcon passwordIcon">
              <img src={keyIcon}  alt="keyIcon"/>
            </div>
            <PasswordViewer showIcon={false} id="newPassword" name="newPassword" placeholder="Enter your new password" />
            <FieldErrorMessage name="newPassword" />
          </div>
        </BSForm.Group>

        <BSForm.Group controlId="confirmPassword" className={`row ${style.formControlGroup}`}>
          <BSForm.Label>Confirm Password</BSForm.Label>
          <div className={style.fromControl}>
            <div className="loginFormIcon passwordIcon">
              <img src={keyIcon}  alt="keyIcon"/>
            </div>
            <Field
              id="confirmPassword"
              name="confirmPassword"
              type="password"
              className={`form-control`}
              placeholder="Re-enter your new password"
            />
            <FieldErrorMessage name="confirmPassword" />
          </div>
        </BSForm.Group>

        <BSForm.Group className={`row border-top pt-4 ${style.formControlGroup}`}>
          <div className={style.fromControl}>
            <Button variant="primary" className="btn-block mb-3" type="submit">Save Password</Button>
            <Button variant="outline-secondary" className="btn-block" type="reset">Reset</Button>
            
          </div>
        </BSForm.Group>
      </Form>
      </Card>
    </div>
  );
}

export default ChangePasswordFormComponent;
