import React, {useState, useCallback} from 'react';
import {faTable} from '@fortawesome/free-solid-svg-icons/faTable';
import {faCogs} from '@fortawesome/free-solid-svg-icons/faCogs';
import {faChartLine} from '@fortawesome/free-solid-svg-icons/faChartLine';
import IconButton from '../../core/Button/IconButton';
import style from './RentRollConfigHeader.module.scss';
import {rentRollAffixTabViewName} from '../../../constants';


function RentRollConfigHeader({
  title, customTitle, activeViewName, isCustomOptionVisible = false, hideChartSwitcher = false, onTabViewChange
}) {
  const {TABLE_VIEW, CHART_VIEW, CONFIG_VIEW} = rentRollAffixTabViewName;

  const onViewChange = useCallback((activeViewName) => {
    onTabViewChange(activeViewName);
  }, [activeViewName]);

  return (
    <>
      <div className={`row align-items-center d-flex ${style.sectionHeader}`}>
        <div className={`pl-2 flex-grow-1 ${style.panelHeader}`}>
          <h3 className="small-heading mb-0">{title}</h3>
        </div>
        {
          !hideChartSwitcher && (
            <div className={`ml-auto ${style.viewIcons}`}>
              <IconButton icon={faTable} title="Table View" onClick={() => onViewChange(TABLE_VIEW)} className={`${activeViewName === TABLE_VIEW ? style.acitve : ''}`} />
              {
                isCustomOptionVisible && <IconButton icon={faCogs} title={customTitle} onClick={() => onViewChange(CONFIG_VIEW)} className={`${activeViewName === CONFIG_VIEW ? style.acitve : ''}`} />
              }
              <IconButton icon={faChartLine} onClick={() => onViewChange(CHART_VIEW)} className={`${activeViewName === CHART_VIEW ? style.acitve : ''}`} title="Chart View" />
            </div>
          )
        }
      </div>
    </>
  );
}

export default RentRollConfigHeader;
