import React from 'react';
import Tour from 'reactour';
import Button from 'react-bootstrap/Button';
import { PROPERTY_TYPES, DocumentFileTypes } from '../../../constants';
import style from './ExtractionPageTour.module.scss';

function ExtractionPageTour({
  show, onClose, document, project
}) {
  const { taggingData: { documentType } } = document;
  const { assetType } = project;

  const extractionPageSteps = (onClose) => {
    if (
      documentType === DocumentFileTypes.RENT_ROLL.key && assetType === PROPERTY_TYPES.MULTIFAMILY.key
    ) {
      return rentRollMFTour(onClose);
    } else if (documentType === DocumentFileTypes.RENT_ROLL.key && (assetType === PROPERTY_TYPES.RETAIL.key || assetType === PROPERTY_TYPES.OFFICE.key || assetType === PROPERTY_TYPES.INDUSTRIAL.key)) {
      return retailRentRollTour(onClose);
    } else if (documentType === DocumentFileTypes.RENT_ROLL.key) {
      return rentRollTour(onClose);
    } else {
      return operatingStatementTour(onClose);
    }
  };

  return (
    <Tour
      steps={extractionPageSteps(onClose)}
      isOpen={show}
      onRequestClose={onClose}
      rounded={5}
      accentColor="#2196f2"
      startAt={0}
      className={style.extractionTour}
      closeWithMask={false}
      disableInteraction={false}
    />
  );
}

