import React from 'react';
import Dropdown from 'react-bootstrap/Dropdown';
import {SpriteFolderIcon, SpriteRenameIcon, SpriteClearTaggingIcon, SpriteDeleteIcon, SpriteNeedSupportIcon} from '../../core/SpriteIcon/SpriteIcon';

function ProjectContextMenu({
  hideContextMenu, onEdit, onDelete, onOpen, projectTemplate, onClearTagging, documentsSummary, onNeedSupport, disableClearTagging
}) {
  const {tagged} = documentsSummary;
  const disableClearTag = tagged === 0 || disableClearTagging;
  return (
    <div>
      <Dropdown.Menu show>
        <Dropdown.Item onClick={() => hideContextMenu() & onOpen()}><SpriteFolderIcon /> Open</Dropdown.Item>
        <Dropdown.Item onClick={() => hideContextMenu() & onEdit()}><SpriteRenameIcon /> Edit</Dropdown.Item>
        <Dropdown.Item onClick={() => hideContextMenu() & onDelete()}><SpriteDeleteIcon /> Delete</Dropdown.Item>
        <Dropdown.Item
          onClick={() => hideContextMenu() & onClearTagging()}
          disabled={disableClearTag}
          className={disableClearTag && "text-secondary"}
        >
          <SpriteClearTaggingIcon /> Clear Tagging
        </Dropdown.Item>
        <Dropdown.Item onClick={() => hideContextMenu() & onNeedSupport()}><SpriteNeedSupportIcon /> Need Support</Dropdown.Item>
      </Dropdown.Menu>
    </div>
  );
}

export default ProjectContextMenu;
