import React from 'react';
import {
  get, capitalize, map, isNil, difference, findKey, each, uniq, sumBy, minBy, maxBy, toString, sortBy
} from 'lodash';
import * as moment from 'moment';
import {saveAs} from 'file-saver';
import CustomFilter, {CustomSelectFilter} from '../components/core/CustomFilter/CustomFilter';
import DisplayDate from '../components/core/DisplayDate/DisplayDate';
import {PROPERTY_TYPES, affixChartColors, DocumentFileTypes, sheetNames} from '../constants';
import config, {appConfig} from '../config';

export function delayedPromise(delay = 500) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, delay);
  });
}

export function forceUTCTimeZone(date) {
  return new Date(Date.UTC(
    date.getFullYear(),
    date.getMonth(),
    date.getDate(),
    date.getHours(),
    date.getMinutes(),
    date.getSeconds(),
    date.getMilliseconds(),
  ));
}

export function loaderManager(p, onShow, onHide, timeout1 = 50, timeout2 = 400) {
  let resolved = false;
  let visible = false;
  let timeoutOver = false;

  const hide = () => {
    if (visible && resolved && timeoutOver) {
      visible = false;
      onHide();
    }
  };

  return new Promise((resolve, reject) => {
    p.then((value) => {
      resolved = true;
      hide();
      resolve(value);
    }, (error) => {
      resolved = true;
      hide();
      reject(error);
    });

    setTimeout(() => {
      if (!resolved) {
        visible = true;
        onShow();
        setTimeout(() => {
          timeoutOver = true;
          hide();
        }, timeout2);
      }
    }, timeout1);
  });
}

export const customSortFunc = nestedSearchKey => (a, b, order) => {
  const compareFieldA = get(a, nestedSearchKey, '');
  const compareFieldB = get(b, nestedSearchKey, '');
  if (order === 'asc') {
    return compareFieldA.localeCompare(compareFieldB);
  }
  return compareFieldB.localeCompare(compareFieldA);
};

export const getCustomFilterElement = (filterHandler, nestedSearchKey) => (
  <CustomFilter
    filterHandler={filterHandler} nestedSearchKey={nestedSearchKey} />
);

export const getCustomSelectFilterElement = (filterHandler, nestedSearchKey, options) => (
  <CustomSelectFilter
    filterHandler={filterHandler} nestedSearchKey={nestedSearchKey} options={options} />
);

export const displayDateFormat = ISOdateString => <DisplayDate date={new Date(ISOdateString)} />;
export const displayNumberInUSFormat = number => (!isNil(number) ? number.toLocaleString('en-EN') : 0);
export const usDateFormat = (ISOdateString) => moment(new Date(ISOdateString)).format('MM/DD/YYYY');

export const displayCurrencyFormat = number => {
  const roundValue = !isNil(number) ? number.toFixed(2) : 0;
  return !isNil(number) ? `$${roundValue.toString().replace(',', '').replace(/\B(?=(\d{3})+\b)/g, ",").replace(/-(.*)/, "($1)")}` : 0;
};

export const displayNumberFormat = number => {
  const roundValue = !isNil(number) ? number.toFixed(2) : 0;
  return !isNil(number) ? roundValue.toString().replace(',', '').replace(/\B(?=(\d{3})+\b)/g, ",").replace(/-(.*)/, "($1)") : 0;
};

export const DisplayAmountFormat = ({number, showCurency = true, appendString = ''}) => {
  const styles = Math.sign(number) === -1 ? {color: 'red', display: 'inline'} : {color: 'inherit', display: 'inline'};
  const roundValue = !isNil(number) ? number.toFixed(2) : 0;
  return (
    !isNil(number) ? <div style={styles}>{`${showCurency ? '$' : ''}${roundValue.toString().replace(',', '').replace(/\B(?=(\d{3})+\b)/g, ",").replace(/-(.*)/, "($1)")}${appendString}`}</div> : <div style={styles}>0</div>
  );
};

export const isStringType = (str) => typeof str === "string" && str;

export const formatAmountTitleTooltip = (number, showCurency = true, appendString = '') => {
  const roundValue = !isNil(number) ? number.toFixed(2) : 0;
  return !isNil(number) ? `${showCurency ? '$' : ''}${roundValue.toString().replace(',', '').replace(/\B(?=(\d{3})+\b)/g, ",").replace(/-(.*)/, "($1)")}${appendString}` : 0;
};

export function capitalizeFirstLetter(str) {
  return isStringType(str) && map(str.split(' '), (w) => capitalize(w)).join(' ');
}

export function isBlankString(str) {
  return (!str || str.length === 0 || !str.trim());
}

export function checkChildKeyHasValue(obj, childKey, value) {
  return Reflect.ownKeys(obj).some((key) => obj[key][childKey] === value);
}

export function checkArrayIncludesValue(array, valueToSearch) {
  return !!array && array.some(value => toString(value).trim().toLowerCase() === toString(valueToSearch).trim().toLowerCase());
}

export function arrayContainsArray(superset, subset) {
  if (subset.length === 0) {
    return false;
  }
  return subset.every((value) => (superset.indexOf(value) >= 0));
}

export function findMaximumLengthOfArrayInObj(objects) {
  let arraySize = 0;
  let foundKeyOfLargestValue = '';
  // eslint-disable-next-line no-restricted-syntax
  for (let item of Reflect.ownKeys(objects)) {
    if (objects[item].length > arraySize) {
      arraySize = objects[item].length;
      foundKeyOfLargestValue = item;
    }
  }
  return foundKeyOfLargestValue;
}

export function findValidKey(objectKeys, key) {
  let foundkey = objectKeys.find(k => k.toLowerCase() === key.toLowerCase());
  return !isNil(foundkey) ? foundkey : '';
}

export const getRandomColor = () => affixChartColors[Math.floor(Math.random() * affixChartColors.length)];

export const getAverage = (totalNumbers, counts) => totalNumbers && Math.round((totalNumbers / counts));

export const validateValue = value => (!isNil(value) ? value : 0);

export const compareTwoArraysInLowerCase = (firstArray, secondArray) => {
  const firstArrayInLowerCase = firstArray.map(value => value.toLowerCase());
  const seondArrayInLowerCase = secondArray.map(value => value.toLowerCase());
  return difference(firstArrayInLowerCase, seondArrayInLowerCase);
};

export function getKeyByValue(obj, value) {
  return findKey(obj, v => v === value);
}

export function getPercentage(number, total) {
  return (number / total) * 100;
}

export function getTotalByKey(collection, keyName) {
  let total = 0;
  each(collection, (item) => {
    if (!isNil(item[keyName])) {
      total = total + Number(item[keyName]);
    }
  });
  return total;
}
const sortDesc = (value1, value2) => {
  if (value1 > value2) {
    return -1;
  } else if (value1 < value2) {
    return 1;
  }
  return 0;
};

const sortAsc = (value1, value2) => {
  if (value1 < value2) {
    return -1;
  } else if (value1 > value2) {
    return 1;
  }
  return 0;
};

export const doSorting = (value1, value2, order) => {
  const sort = order === 'desc' ? sortDesc : sortAsc;
  return sort(value1, value2);
};

export const isBracketExists = (str) => {
  const regex = /\(.*\)/;
  return regex.test(str);
};

export const getIsDocumentReExtractRequired = (document, isMaskingTemplate) => {
  if (document.taggingData && document.taggingData.documentType === 'RENT_ROLL') {
    if (document.extractionJob && document.extractionJob.status) {
      if (isMaskingTemplate) {
        return false;
      }
      const {createdAt} = document.extractionJob;
      const {reExtractBeforeDate} = config;
      const createdAtDate = moment(createdAt);
      const reExtractDate = moment(reExtractBeforeDate, 'MM/DD/YYYY');
      return createdAtDate.isBefore(reExtractDate);
    } else {
      return false;
    }
  } else {
    return false;
  }
};

export function isNumberBetweenRange(number, min, max) {
  return number >= min && number <= max;
}

export function validateChargeCode(chargeCode) {
  const updatedChargeCode = chargeCode.replaceAll("_", " "); // removing underscore from the charge code
  return updatedChargeCode.indexOf('cc') > -1 ? updatedChargeCode.substr(2) : updatedChargeCode;
}

export function constructProjectXlsxAndModelName(project, restrictedStrings) {
  const sanitizedModelName = project.name.replace(/[^a-zA-Z0-9 ]/g, '');
  if (restrictedStrings[project.assetType] && sanitizedModelName.indexOf(restrictedStrings[project.assetType]) === 0) {
    return project.assetType === PROPERTY_TYPES.MULTIFAMILY.key ?
      sanitizedModelName.substring(2) :
      sanitizedModelName.substring(3);
  }
  return sanitizedModelName;
}

export function normalize(list, key) {
  return list.reduce((obj, item) => {
    obj[get(item, key)] = item;
    return obj;
  }, {});
}

export function removeNullEmptyValues(values) {
  return uniq(values).filter(value => !isNil(value) && !!value.length);
}

export function uniqueByValue(arr, key) {
  return arr.reduce((a, d) => {
    if (!a.includes(d[key])) {a.push(d[key]);}
    return a;
  }, []);
}

export function compareTwoStrings(sourceText, targetText) {
  let isEqualText = false;
  if (!isNil(sourceText) && !isNil(targetText)) {
    if (isStringType(sourceText) && isStringType(targetText)) {
      isEqualText = sourceText.toLowerCase().replace(/\s/g, '') === targetText.toLowerCase().replace(/\s/g, '');
    }
  }
  return isEqualText;
}

export function getJsonFileData(fileData, fileName) {
  try {
    return JSON.parse(fileData);
  } catch (e) {
    throw new Error(`Invalid Json File: ${fileName} - ${e}`);
  }
}

export function isStringFunction(funcString) {
  try {
    // eslint-disable-next-line no-unused-vars
    let evalFunc;
    // eslint-disable-next-line no-eval
    const result = eval(`evalFunc = ${funcString}`);
    if (result instanceof Function) {
      return;
    } else {
      throw new Error("Function string is not a function");
    }
  } catch (e) {
    throw new Error(`Error in function string: ${e}`);
  }
}

export function getTotalSum(object, key) {
  return !isNil(sumBy(object, key)) ? sumBy(object, key) : 0;
}

export function getMinValue(object, key) {
  const minValue = minBy(object, key);
  return !isNil(minValue) ? minValue[key] : 0;
}

export function getMaxValue(object, key) {
  const maxValue = maxBy(object, key);
  return !isNil(maxValue) ? maxValue[key] : 0;
}

export function convertDataListDropDown(dropdownValue) {
  const listDropDown = [];
  !!dropdownValue.length && dropdownValue.forEach(value => listDropDown.push({item: value, value}));
  return listDropDown;
}

export function isOSBaseType(documentType) {
  return DocumentFileTypes[documentType].baseType === DocumentFileTypes.OPERATING_STATEMENT.baseType;
}

export function isRRBaseType(documentType) {
  return DocumentFileTypes[documentType].baseType === DocumentFileTypes.RENT_ROLL.baseType;
}

export const isMFBaseType = (assetType) => PROPERTY_TYPES[assetType].baseClass === PROPERTY_TYPES.MULTIFAMILY.baseClass;
export const isRetailBaseType = (assetType) => PROPERTY_TYPES[assetType].baseClass === PROPERTY_TYPES.RETAIL.baseClass;
export const isHotelBaseType = (assetType) => PROPERTY_TYPES[assetType].baseClass === PROPERTY_TYPES.HOTEL.baseClass;

export const json2CSVConverter = (items, headers) => {
  const replacer = (key, value) => (value === null ? '' : `${value}`);
  let csv = items.map(row => headers.map(fieldName => JSON.stringify(row[fieldName], replacer)).join(','));
  csv.unshift(headers.join(','));
  csv = csv.join('\r\n');
  return csv;
};

export const saveAsFile = (text, filename) => {
  const blob = new Blob([text], {type: "text/plain;charset=utf-8"});
  saveAs(blob, filename);
};

export const exportCSVFile = (listItems, headers, fileName) => {
  const csvText = json2CSVConverter(listItems, headers);
  saveAsFile(csvText, fileName);
};

export const isNotEmptyOrNaNValue = (object, key) => object[key] !== "" && !isNaN(object[key]);

export const extractTextBetweenBracket = (text) => text.match(/\(([^)]+)\)/)[1];

export const fetchBaseTypeByAssetType = (assetType) => {
  const baseClass = Reflect.ownKeys(PROPERTY_TYPES).map(assetClass => PROPERTY_TYPES[assetClass]).filter(f => f.key === assetType);
  return baseClass.length ? baseClass[0].baseClass : baseClass;
};

export const isFreeBieApp = appConfig.isFreebieApp;

export const extractNumberFromString = (str) => !!str && Number(str.replace(/\D+/g, ''));

export const sortPeriodDateByYear = (periodDates) => {
  let yearWithPeriodDiffrence = null;
  if (periodDates) {
    const sortPeriodByYear = sortBy(periodDates, (date) => moment(date.periodFrom, 'MM/DD/YYYY').format('X'));
    yearWithPeriodDiffrence = sortPeriodByYear.reduce((obj, date) => Object.assign(obj, {[moment(date.periodFrom, 'MM/DD/YYYY').format('YYYY')]: moment(date.periodTo, 'MM/DD/YYYY').diff(moment(date.periodFrom, 'MM/DD/YYYY'), 'months') + 1}), {});
  }
  return yearWithPeriodDiffrence;
};

export const checkValidNumber = (value) => /^[+-]?(?=.)(?:\d+,)*\d*(?:\.\d+)?$/.test(value);